<template>
	<v-container class="mt-5">
	  <h1>Confirmation Page</h1>
	  <v-flex offset-lg4 lg4 md6 xs12 class="px-3">
	      	<div class="people confirm-card">
				<div class="col-md-7 text-center" style="margin-top: +12px;">
					<p class="stats-val text-xs-center mt-5 pt-3">{{autherrorMsg}}</p>
				</div>
			</div>
	      </v-flex>
	      <spinner :show.sync="loading"/>
	</v-container>
</template>

<style type="text/css" scoped>
	
	.confirm-card {
		height: 200px;
	}
</style>

<script type="text/javascript">
	import { mapGetters, mapActions } from "vuex";
	export default {
		mounted(){
			this.confirmAccount();
		},
		methods: {
			...mapActions('auth', ['confirm_email']),
			confirmAccount(){
				var args = {
					id: this.$route.params.id,
					token: this.$route.params.token
				};
				console.log(args);
				this.confirm_email(args)
				.then((response)=>{
					if(response.status){
						this.$router.push({path:'/login'});
					}
				})

			}
		},
		computed: {
			...mapGetters('auth', ['loading','autherror', 'autherrorMsg']),
		}
	}
</script>




