<template>
	<div class="container">
		<section v-if="ready">
			<div class="row px-4 mt-3">
				<div class="col-xs-10">
					<h5 class="heading">Unconfirmed Admins ({{unconfirmed.length}})</h5>
				</div>
				<div class="col-xs-1">
					
				</div>
		    
			</div>
		</section>
		<section v-if="loading">
			<div class="row px-4 mt-3 text-center">
				<span>
					<i large class="blue--text lighten-2 fa fa-spinner fa-spin fa-3x"></i>
				</span>	    
			</div>
		</section>

		<div v-else>
		<section v-if="unconfirmed.length === 0">
			<div class="row px-4 mt-3 text-center">
				<p class="pt-5 mt-5">
					<i large class="blue--text lighten-2 fa fa-folder-open-o fa-3x"></i>
					<p class="heading">No Unconfirmed Accounts</p>
				</p>	    
			</div>
		</section>

		<section v-else>
			<div class="row px-4 mt-3">
				<div class="col-xs-12 col-md-6 col-lg-3" v-for="type in unconfirmed" :key="type.Id" >
			      	<card  @refresh="fetchAdmins" @block="blocks" :admin="type"/>
			    </div>
			</div>
		</section>
		</div>    
	    
	</div>
</template>
<style type="text/css" scoped>
	.no-games {
		text-align: center;
	}
	.wht {
		line-height: 100px;
	}
	.count {
		line-height: 20px;
		font-size: 18px;
		font-style: italic;
	}


</style>


<script type="text/javascript">
  import { mapGetters, mapActions } from "vuex";
  import card from './newAdminCard'

  export default {
 	data(){
 		return {
 			ready: false,
 			newGroup: {},
 			group_dialog: false,
 			selected_group: null,
 		}
 	},
 	components:{card},
 	mounted(){
 		this.selected_group = 'all'
 		this.fetchAdmins();
 	},
 	computed: {
		...mapGetters('user_mgt', [
			'user',
            'session',
            'admins',
            'loading',
            'unconfirmed'
		]),
		filteredAdmins(){
			var g = this.selected_group;
			if(g == null || g == 'all'){
				return this.admins
			}else{
				var t = this.admins.filter(e=>e.group == g);
				return t;
			}
		},
		
		
 	},
 	methods: {
 		...mapActions('user_mgt', ['confirmAccount','fetchUnconfirmed']),
 		fetchAdmins(){
 			var args = {
 				session: this.session,
 			}
 			this.fetchUnconfirmed(args)
 			.then(()=>{
 				this.ready = true;
 			})
 		},

 		fetchGroup(){
 			var args = {
 				session: this.session,
 			}
 			this.fetchGroups(args)
 			.then(()=>{
 				this.ready = true;
 			})
 		},
 		saveGroup(){
 			var args = {
 				session: this.session,
 				group: this.newGroup
 			}
 			this.makeGroup(args)
 			.then(()=>{
 				this.fetchGroup()
 			})
 		}, 		
		gameDetails(game){
			return JSON.parse(game.details);
		},
 		openGameConfig(){
 			this.$router.push({name: 'game-config'});
         },
         blocks(args){
             args.session = this.session;
             var self = this;
             this.accountBlock(args).
             then((resp)=>{
                 if(resp.status){
                     self.fetchAdmins();
                     self.$notify({
                        type: 'success',
                        message: resp.message
                    });
                 }else{
                     self.$notify({
                        type: 'error',
                        message: resp.message
                    });
                 }
             })
         }
 	}	
  }
</script>




