<template>
  <div class="container">
		<div class="row">
			<div class="col-xs-12 col-md-4 col-lg-3"></div>
			<div class="col-xs-12 col-md-4 col-lg-6 people text-center">
				<img :src="require('@/assets/lottery.png')" style="width: 136px;" alt="avatar">

				<form class="row text-center">
					<div class="col-md-1 col-lg-2"></div>
					<div class="col-xs-12 col-md-10 col-lg-8">
						<label class="mt-2 left">Email</label>
					    <el-input class="mt-2 mb-2"
					      v-model="user.email"
					      :rules="emailRules"
					      label="E-mail"
					      :counter="20"
					      required
					    ></el-input>
					    <label class="mt-2 left">Password</label>
					    <el-input class="mt-2 mb-2"
					       v-model="user.password"
					      label="Enter your password"
					      min="8"
					      value=""
					      error
					      :type="value?'password':'text'"
					    	>
					      <el-button slot="append" @click="()=>{value = !value}"
					      :class="value?'fa fa-eye':'fa fa-eye-slash'"></el-button>
						</el-input>
						<label class="mt-2 left">First Name</label>
					    <el-input class="mt-2 mb-2"
					      v-model="user.first_name"
					      placeholder="Your First Name"
					      required
					    ></el-input>
					    <label class="mt-2 left">Last Name</label>
					    <el-input class="mt-2 mb-2"
					      v-model="user.last_name"
					      placeholder="Your Last Name"
					      required
					    ></el-input>
					    <label class="mt-2 left">Security Question</label>
					   	<el-select v-model="user.question" style="width: 100%;" class="mt-2 mb-2" clearable
					   	placeholder="Select A Question">
					    <el-option
					      v-for="item in items"
					      :key="item"
					      :label="item"
					      :value="item">
					    </el-option>
					  </el-select>
					  <label class="mt-2 left">Answer to Question</label>
					    <el-input class="mt-3"
					      v-model="user.answer"
					      placeholder="Answer to Question"
					      required
					    ></el-input>
					</div>
				</form>
				<div class="row text-center mt-4 mb-5">
					<el-button :loading="loading" :disabled="loading" color="success" class="deep-purple white--text" @click="LoginBtn">Signup</el-button>
				</div>
			</div>
			<div class="col-xs-12 col-md-4 col-lg-3"></div>
		</div>
	</div>
</template>

<script type="text/javascript">
import { mapGetters, mapActions } from "vuex";

  import spinner from '@/app/shared/spinner'

	export default {
		components:{spinner},
		data(){
			return {
				user: {},
			  valid: false,
		      dialog: false,
		      value: true,
		      nameRules: [
		        v => !!v || 'Name is required',
		        v => (v && v.length <= 20) || 'Name must be less than 21 characters'
		      ],
		      email: '',
		      emailRules: [
		        v => !!v || 'E-mail is required',
		        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
		      ],
		      select: null,
		      items: [
		        "What is Your Mother's Maiden Name?",
		        'Who was your first crush?',
		        'When did you have your first kiss',
		      ],
		      checkbox: false
			}
		},
		mounted(){
			this.clear();
		},
		computed: {
			...mapGetters('auth',['loading','regSuccess']),
			clearFields(){
				this.clear();
			}
		},
		methods:{
			...mapActions('auth',[
		        'signup',
		        'clearErrors'
		        ]),
			closeLogin(){
				this.$emit('close');
			},
			LoginBtn(){
				 var self = this;
		        this.signup(this.user).then((reply)=>{
			        if(reply.status){
			        	this.$router.push({name: 'confirmation_sent'})
			        	self.$notify({
				          title: 'Signup Success',
				          message: reply.message,
				          type: 'success'
				        });
			        }else{
			        	self.$notify({
				          title: 'Signup Failed',
				          message: reply.message,
				          type: 'error'
				        });
			        }
		        });
			},
		      clear () {
		       	this.email = '';
				this.password = '';
				this.question = '';
				this.answer = '';
				this.name = '';
		      }

		}
	}
</script>

<style type="text/css">
	.login-card {
		height: 100%;
		padding: 15px;
		width: auto;
	}
</style>


