<template>
	<v-container class="row">
	  <v-layout row wrap>
	      <v-flex lg6 md6 xs12 class="px-1">{{validated}}
	      	<label>Total Balls</label><br>
	      	<el-input-number v-model="game.total_balls"  class="pb-3" style="width: 90%" :min="1" 
			  :max="100" v-validate="'required'" name="total_balls" label="Total Balls"></el-input-number><br>
	      	
	      	<label  v-if="game.type == '3'">Free Balls</label><br v-if="game.type == '3'">
	      	<el-input-number v-if="game.type == '3'" v-model="game.free_balls" name="free_balls" v-validate="'required'" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
	      	<label>Price/Ball</label><br>
	      	<el-input-number v-model="game.price" v-validate="'required'" name="price" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
	      	<label>Authentication Type</label>
	      	<el-select v-model="game.auth_type" v-validate="'required'" name="auth_type" placeholder="Select a type" class="pb-3" style="width: 90%">
	      	    <el-option v-for="item in options"
	      	        :key="item.value"
	      	        :label="item.label"
	      	        :value="item.value">
	      	    </el-option>
	      	</el-select>
	      </v-flex>
	      <v-flex lg6 md6 xs12 class="px-1">
	      	<label>Minimum Paid Numbers</label><br>
	      	<el-input-number v-model="game.min_paid" v-validate="'required'" name="min_paid" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number><br>
	      	<label>Winning Prize</label><br>
	      	<el-input v-model="game.prize" placeholder="" v-validate="'required'" name="prize" style="width: 90%" class="pb-3"></el-input><br>
	      	<label>Prize Description</label><br>
	      	<el-input v-model="game.prize_description" v-validate="'required'" name="prize_desc" style="width: 90%" placeholder=""></el-input>
	      	 <el-checkbox v-if="game.type !== '4'" v-model="game.others" class="pt-3 mt-4">Show on 'Other Games' List</el-checkbox>

	      </v-flex>
	  </v-layout>
	</v-container>
</template>

<style type="text/css">
	
</style>

<script type="text/javascript">
	import { mapGetters, mapActions } from "vuex";
	export default {
		data(){
			return {
				options: [
					{
						value: '1',
						label: 'Automatic'
					},
					{
						value: '2',
						label: 'Manual'
					}
				]
			}
		},
		methods: {
			nextStep(){
				this.$validator.validateAll().then(result => {
		            if (result) {
		                this.$emit('next');
		                return
		            }else{
		            	this.$notify({
				          title: 'Some Details are Missing',
				          type: 'error'
				        });
		            }
		        });
			},
			backStep(){
				this.$emit('back');
			}
		},

		computed: {
			...mapGetters('persistent', ['game']),
			validated(){
				var val = false;
				this.$validator.validateAll().then(result=>{
					if(result){
						return this.game.validated = true;
					}else{
						return this.game.validated = false;
					}
				});

				
			}
		}
	}
</script>


