<template>
    <div class="people game-card mx-1">
				<div class="text-xs-center" style="margin-top: +12px;">
                    <p>
                        {{admin.first_name+' '+admin.last_name}}
                    </p>
                    <p>
                        {{admin.email}}
                    </p>
                    <p>
                        {{admin.confirmed?'Member Since':'Registered On'}} {{readAbleTime(Number(admin.created_date))}}
                    </p>
                    <p v-if="admin.confirmed">
                        <el-tooltip v-if="admin.blocked" content="Unlock This Admin" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="blockAccount(false)">
								<i class="fa fa-unlock"></i>
							</el-button>
						</el-tooltip>
						<el-tooltip v-else content="Block This Admin" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="blockAccount(true)">
								<i class="fa fa-lock"></i>
							</el-button>
						</el-tooltip>

						
						
						
						<el-tooltip content="Change Permissions" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="editGame">
								<i class="fa fa-edit"></i>
							</el-button>
						</el-tooltip>
						
                        
					</p>
					<p v-else>
						<el-tooltip content="Confirm Account" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="confirm">
								<i class="fa fa-check"></i>
							</el-button>
						</el-tooltip>
					</p>
				</div>

				<Permissions :admin="admin" :show="edit" v-if="edit" @savePerms="updatePermissions" @close="closeEdit" :id="admin.Id" :permissions="admin.permissions"/>
				<!-- <Preview :show="preview" @close="closeEdit" :id="game" :game="details"/> -->
				</div>
</template>

<script>
import Permissions from "./permissions";
import Preview from "./previewGame";
  import { mapGetters, mapActions } from "vuex";

export default {
	props:['admin'],
	components: {Permissions, Preview},
	data(){
		return {
			edit: false,
			preview: false,
            currentGame: {},
		}
	},
	methods: {
		...mapActions('games',['adminGames','deleteGames']),
        ...mapActions('user_mgt', ['fetchUnconfirmed','accountBlock']),
		editGame(){
            this.edit = true;
        },
        updatePermissions(perms, id){

            var args = {};
            args.session = this.session;
            args.user = this.admin;
            var self  = this;
            this.updatePerm(args)
            .then((res)=>{
                if(res.status){
                    var args = {
                        session: self.session,
                    }
                    self.fetchUnconfirmed(args)
                    .then(()=>{
                        self.$notify({
                            type: 'success',
                            message: res.message
                        });
                    })
                     
                }
            });
        },
		closeEdit(loop){

			if(loop == 1){
				this.$emit('refresh');
			}
			this.edit = false;
			this.preview = false;
		},
		previewGame(){
			this.preview = true;
        },
        readAbleTime(d){
            var x = new Date(d);
            var time = x.toDateString();
            return time
        },
        confirm(){
            var args = {};
            var self = this;
			this.$confirm('Are you sure you want to confirm this Admin?', 'Warning', {
                confirmButtonText: 'Yes, I\'m sure',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(()=>{
                this.admin.confirmed = true;
                self.updatePermissions();
            }).catch(()=>{

            });
		},
		blockAccount(state){
            var args = {};
            var self = this;
			this.$confirm('Are you sure you want to block this Admin?', 'Warning', {
                confirmButtonText: 'Yes, I\'m sure',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(()=>{
                this.admin.blocked = state;
                self.updatePermissions();
            }).catch(()=>{

            });
		},
		goLive(){
			this.$router.push({name: 'live-center', params: {gameId: this.game}});
        },
        ...mapActions('user_mgt', ['fetchPerms','updatePerm']),

	},
	computed: {
		...mapGetters('user_mgt', [
			'user',
            'session',
            'perms'
		]),
		...mapGetters('games', [
			'live_game',
			'loading',
			'mini_games',

		]),
	},

}
</script>




