<template>
    <el-dialog
        title="Act on this Pick"
        :visible.sync="show"
        :width="calcWidth" :before-close="closeModal"
        @close="closeModal">
        <p>
            <label>Set Player Name</label><br>
        <el-input v-model="item.player" class="py-2" placeholder=""></el-input>
        </p>

        <span slot="footer">
            <div class="apo"></div>
            <el-button @click="closeModal" size="small">Close</el-button>
            <el-button type="primary" @click="SaveEdit" size="small">Save Changes</el-button>
        </span>
    </el-dialog>
    
</template>

<script>
export default {
    props:['show','item','type','details'],
    data(){
        return{
            
        }
    },
    methods: {
        closeModal(){
            this.$emit('close',0);
        },
        SaveEdit(){
            this.$emit('saveChanges',this.item);
        },
        accept(){
          this.$emit('accept');  
        },
        deny(){
            this.$emit('deny');
        },
        setWinner(){
            this.$emit('winner');
        }
        
    },
    computed: {
        calcWidth(){
            var resdif = (screen.width/screen.height);
            if (resdif >= 1.6) {
                return '60%';
            }
            else {
                return '90%';
            }
        }
    }
}
</script>
<style scoped>
.copy-icon {
    font-size: 18px;
}
</style>





