<template>
    <div class="">
        <div class="row">
            <div class="col-xs-12 col-md-1 col-lg-1 mb-4">
                <div class="row">
                    <div class="col-xs-3 col-lg-12">
                        <el-button circle class="indigo lighten-4 white--text" @click="openShare" size="small">
                            <i class="fa fa-share"></i>
                        </el-button>
                    </div>

                    <div class="col-xs-3 col-lg-12">
                        <el-button  circle class="lime darken-1 white--text" @click="openEdit"  dark size="small">
                            <i class="fa fa-edit"></i>
                        </el-button>
                    </div>

                    <div class="col-xs-3 col-lg-12">
                        <el-button  @click="donate" circle class="deep-purple white--text" fab dark size="small">
                            <i class="fa fa-paypal"></i>
                        </el-button>
                    </div>
                     <div class="col-xs-3 col-lg-12">
                        <el-button  circle class="teal darken-4 white--text" @click="newGame" fab dark size="small">
                            <i class="fa fa-plus"></i>
                        </el-button>
                    </div>
                </div>

            </div>
            <div class="col-xs-12 col-md-7 col-lg-7">
                <div class="well" v-if="(game.type=='4') && !game.game_completed && miniCompleted">
                    <el-button  type="success" @click="randomWinners">Random Winners</el-button>
                </div>

                <div class="table-responsive">
                    <table class="table table-hover">
                        <thead>
                          <tr>
                            <th>
                                Balls
                            </th>
                            <th >
                                Player
                            </th>
                            <th v-if="game.type == '1'">
                                Teams
                            </th>
                            <th v-if="!game.game_completed">
                                Action
                            </th>
                            <th v-if="all_is_done&&!game.game_completed">
                                Winner
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(item, index) in Data" :key="index" style="padding-top: 10px;">
                            <td class="el-table_3_column_8" width="100px">
                                <el-button circle :class="getColor(item.status)" class="white--text" dark style="width: 40px; height: 40px;"> 
                                    {{item.ball}}
                                    <v-badge :color="getColor(item.status)" :class="{'add-margin':Number(item.ball)>9,'add-margin-2':Number(item.ball)<10}" v-if="item.paid">
                                        <i slot="badge" class="fa fa-paypal"></i>
                                    </v-badge>
                                </el-button>
                            </td>
                            <td>
                                <el-badge :value="isWinner(item.ball)" class="item">
                                    <span>
                                        {{item.player}}
                                        <a v-if="item.player !=='Not picked yet'" :href="'https://tools.keycdn.com/geo?host='+item.ip" target="_blank" class="buton fa fa-map-marker" title="Trace"></a>
                                    </span>
                                    <br>
                                    <span v-if="item.player !=='Not picked yet'">{{getDate(item.time)}}
                                        <a href="#" v-if="!game.game_completed" @click.prevent="openSingleEdit(item, index)" class="buton fa fa-edit" title="Edit"></a>
                                    </span>
                                </el-badge>
                            </td>
                            <td v-if="game.type == '1'">
                                {{item.team}}
                            </td>
                            <td width="50px">
                                <el-switch @change="paymentSent(index, item.paid)" v-if="item.status=='paid' && !game.game_completed" active-text="P" v-model="item.paid">
                                </el-switch>
                                <el-button type="primary" size="small"
                                  @click.prevent="accept(index)" v-if="item.status=='picked' && game.type == 3" title="Accept">A</el-button>
                                  <el-button type="primary" size="small"
                                   @click.prevent="deny(index)" v-if="item.status=='picked' && game.type == 3" title="Deny">D</el-button>
                                  
                            </td>
                            
                            <td v-if="game.type !== '4'  && !game.game_completed">
                                 <el-button type="primary" size="small"
                                   @click.prevent="setWinner(index)" v-if="all_is_done">W</el-button>
                            </td>

                        </tr>
                        <tr v-if="Data.length < 1">
                            <td class="el-table_3_column_8" align-center colspan="6">
                                No Data found for this game
                            </td>
                        </tr>
                        </tbody>
                      </table>
                </div>
            </div>
            <div class="col-xs-12 col-md-4 col-lg-4">
                <h3>Game Details</h3>
                <div class="table-responsive">
                    <table class="table table-hover">
                        <tbody v-if="game">
                            <tr>
                            <td class="el-table_3_column_8" colspan="2">
                                <a :href="setUrl" class="btn btn-info btn-block" style="padding-top: 10px; padding-bottom: 10px;" target="_blank">View As Player</a>
                            </td>
                        </tr>
                          <tr>
                            <td class="el-table_3_column_8">
                                Total Balls
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.total_balls||0}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Free Balls
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.free_balls||0}}
                            </td>
                        </tr>

                         <tr v-if="game.type==3 && user.super">
                            <td class="el-table_3_column_8">
                                Free Numbers
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.frees.toString()}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Game Type
                            </td>
                            <td class="el-table_3_column_8">
                               {{$_$gameTypes.find(e=>e.value==game.type).name}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Price
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.price}} GBP
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Remaining Paid
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.left_paid}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Remaining Free
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.left_free||0}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Prize
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.prize}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Prize Description
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.prize_description}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Prize Photo
                            </td>
                            <td class="el-table_3_column_8">
                               <el-button type="text" @click="prev_photo = true">Preview Prize</el-button>
                            </td>
                        </tr>
                        </tbody>
                        <tbody v-else>
                            <tr>
                                <td class="el-table_3_column_8" align-center colspan="6">
                                No Data found for this game
                            </td>
                            </tr>
                        </tbody> 
                      </table>
                </div>
            </div>
        </div>


         <Min :show="minPaid" @save="accept" :players="players" :game="game"/>
        <PickChange @saveChanges="pickEditSave" 
        :show="editSingle" :item="singlePick" 
        :type="game.type" :details="game" v-if="editSingle" 
        @accept="accept" @deny="deny" @winner="setWinner"
        @close="refreshData"/>
      <Editor :show="edit" @close="closeEdit" v-if="edit" :id="$route.params.gameId" :game="game"/>
        <Preview :show="preview" @close="closeEdit" :id="$route.params.gameId"/>
        <spinner :show.sync="loading"/>
        <el-dialog :visible.sync="prev_photo" v-if="prev_photo" width="70%">
            <img width="100%" :src="$API+'/contents/'+game.prize_photo" alt="">
        </el-dialog>
    </div>
</template>


<script>
import Min from './min_paid';
import { mapGetters, mapActions } from "vuex";
import Preview from "./previewGame";
import PickChange from "../shared/editPick";

import Editor from "./editGameModual";
import vueinterval from 'vue-interval/dist/VueInterval.common'

export default {
    mixins:[vueinterval],
    components: {Preview, Editor, PickChange, Min},
    data(){
        return {
             headers: [
                {
                    text: 'Balls',
                    align: 'center',
                    value: 'balls'
                },
                { text: 'Player', value: 'player' },
                { text: 'Ip Address', value: 'ip' },
                { text: 'Status', value: 'status' },
                { text: 'Timestamp', value: 'timestamp' },
                { text: 'Action', value: 'action' }
                ],
                details: null,
                preview: false,
                edit: false,
                prev_photo: false,
                curG: null,
                refreshTime: 0,
                editSingle: false,
                singlePick: null,
                singleIndex: null,
                classo: '',
        }
    },
    mounted(){
        this.fetchLive();
    },
    methods: {
        ...mapActions('live_admin', [
            'getLiveGame',
            'refresh',
            'updatePicks'
        ]),
        donate(){
            var win = window.open('http://www.paypal.me/bleemster/1', '_blank');
            win.focus();
        },
        openSingleEdit(item, index){
            this.singlePick = item;
            this.singleIndex = index;
            this.editSingle = true;
        },
        INTERVAL__1e4$myUpdateMethod: function () {  
        this.calcWidth();          
            this.refreshTime+=10;
            if(this.refreshTime == 20){
                if(!this.miniCompleted){
                    this.refreshData();
                }
                this.refreshTime = 0;
            }
        },
        
        fetchLive(){
            var args = {
                Id: this.$route.params.gameId,
                session: this.session
            };
            this.getLiveGame(args).
            then(result=>{
                this.curG = this.game;
                if(this.game.winnerArray){

                }else{
                    this.game.winnerArray = [];
                }
                // this.refresh = setInterval(this.refreshData, 30000);
            })
        },
        pickEditSave(item){            
            this.game.picks[this.singleIndex] = item;
            var args = {
                game: this.game,
                id: this.$route.params.gameId
            };
            this.updatePicks(args).
            then(edr=>{
                this.fetchLive();
                this.editSingle = false;
            })
        },
        newGame(){
            this.$router.push({name: 'game-config'})
        },
         refreshData(){
            var args = {
                Id: this.$route.params.gameId,
                session: this.session
            };
            this.refresh(args).
            then(result=>{
                this.curG = this.game;
                this.editSingle = false;
            })
        },
        closeEdit(){
            this.preview = false;
            this.edit = false;
            this.refreshData();
        },
        openEdit(){
            this.edit = true;
        },
        openShare(){
            this.preview = true;
        },
        
        accept(ind){
            var index = this.singleIndex;
            if(ind !== undefined){
                index = ind;
            }
            var g = this.game;
            
            if(g.type == '3'){
                if(g.frees.filter(e=>e==g.picks[index].ball).length > 0){
                    g.picks[index].status = 'free';
                    g.left_free = g.left_free - 1;
                }else{
                    g.picks[index].status = 'paid';
                    g.left_paid = g.left_paid - 1;
                }
            }else{
                g.picks[index].status = 'paid';  
                g.left_paid = g.left_paid - 1;              
            }
           var args = {
                game: g,
                id: this.$route.params.gameId
            };
            this.updatePicks(args).
            then(edr=>{
                this.fetchLive();
                this.editSingle = false;
            })
        },
        deny(index){
            var ball = this.singleIndex;
            if(ind !== undefined){
                ball = ind;
            }
            var g = this.game
                g.picks[ball].status = 'Not picked yet';
                g.picks[ball].ip = 'Not picked yet';
                g.picks[ball].player = 'Not picked yet';
                g.picks[ball].time = 'Not picked yet';
                var args = {
                    game: g,
                    id: this.$route.params.gameId
                };
                this.updatePicks(args).
                then(edr=>{
                    this.fetchLive();
                    this.editSingle = false;
                })
        },
        paymentSent(index, state){
            // console.log(state);
            var ball = index;
            var g = this.game;
                g.picks[ball].paid = state;
                var args = {
                    game: g,
                    id: this.$route.params.gameId
                };
                this.updatePicks(args).
                then(edr=>{
                    this.fetchLive();
                })
        },
        randomWinners(){

            if(this.game){
                var g = this.game;
                var db =  [];
                for (var i = 1; i < Number(g.total_balls)+1; i++) {
                    db.push(i)
                }
                var winners = g.winners || 1;
                g.winnerArray = this.shuffleArray(db).slice(0, Number(winners)); 
                g.game_completed = true;
                var args = {
                    game: g,
                    id: this.$route.params.gameId
                };
                this.updatePicks(args).
                then(edr=>{
                    this.fetchLive();
                })
                               
            }
        },
        shuffleArray(array) {
          for (var i = array.length - 1; i > 0; i--) {
              var j = Math.floor(Math.random() * (i + 1));
              var temp = array[i];
              array[i] = array[j];
              array[j] = temp;
          }
          return array;
      },
        setWinner(index){

            var ball = this.singleIndex;
            if(index !== undefined){
                ball = index;
            }
            var g = this.game;
                g.winnerArray = [];
                var ba = g.picks[ball].ball;
                g.game_completed = true;
                g.winnerArray.push(ba);
                var args = {
                    game: g,
                    id: this.$route.params.gameId
                };
                this.updatePicks(args).
                then(edr=>{
                    this.fetchLive();
                    this.editSingle = false;
                })
        },

        min_paid_complete(game){
                var args = {
                    game: game,
                    id: this.$route.params.gameId
                };
                this.updatePicks(args).
                then(edr=>{
                    this.fetchLive();
                    this.editSingle = false;
                })
        },
        isWinner(item){
            if(this.game){
                if(this.game.winnerArray){
                    if(this.game.winnerArray.includes(item)){
                        var position = this.game.winnerArray.indexOf(item);
                        position++;
                        return this.positional(position)+" Winner!!!";
                    }                    
                }
            }
        },
        positional(it){
            if(it == 1){
                return '1st';
            }else if(it == 2){
                return '2nd';
            }else if(it == 3){
                return '3rd';
            }else if(it > 3){
                return it+'th';
            }
        },
        getColor(status){
            var color = 'light-green darken-4';
            if(status=='paid'){
                color = 'amber darken-4'
            }
            if(status =='free'){
                color='indigo darken-3';
            }
            return color;
        },
        getDate(time){
            if(time !== 'Not picked yet'){
                var day = new Date(time).getDate();
                var month = new Date(time).getMonth();
                var year = new Date(time).getFullYear();
                var hours = new Date(time).getHours();
                var min = new Date(time).getMinutes();
                var sec = new Date(time).getSeconds();
                var times = day+'/'+month+'/'+year+' - '+hours+':'+min+':'+sec;
                return times;
            }else{
                return time;
            }
        },
        calcWidth(){
            var resdif = (screen.width/screen.height);
            if (resdif >= 1.6) {
                return '60%';
            }
            else {
                return '90%';
            }
        },
        

    },
   
    computed: {
        ...mapGetters('user_mgt', [
			'user',
			'session'
		]),
        setUrl(){
            var url = location.origin+'/player/dashboard/'+this.game.Id;
            return url;
        },

        all_is_done(){
            var frr = 0;
            if(this.game.free_balls){
                frr = this.game.free_balls;
            }
            var paid_numbers = this.game.total_balls - frr;
            var con1  = this.game.picks.filter(e=>e.status ==='Not picked yet').length === 0,
            con2 = this.game.picks.filter(e=>e.paid).length === paid_numbers,
            con3 = this.game.type == '1';
            if(con1 && con2 ){
                return true
            }
            return false;
        },        
		...mapGetters('live_admin', [
			'game',
			'loading',
        ]),
        gCompl(){
            var not_done = this.game.picks.filter(e=>e.status==='Not picked yet').length;
            if(not_done > 0){
                return false;
            }
            return true;
        },
        tableData(){
            var tb = [];
            tb = [
        		{label: 'ID', value: this.details?this.game.game_id:''},
        		{label: 'Game Type', value: this.details?this.$_$gameTypes.find(e=> e.value === this.details.type).name:''},
        		{label: 'Total Balls', value: this.details?this.details.total_balls:''},
        		{label: 'Price', value: this.details?''+this.details.price:'0'},
        	];
        	return tb;
        },
        photo(){
            var t = this.details?this.details.prize_photo:'';
            return this.$API+t;
        },
        Data(){
            var d = this.game.picks;
            console.log(d);
            return d;
        },
        type(){
            if(this.details){
                var d = this.details.type;
                return d;
            }else{
                return '0';
            }
            
        },
        players(){
            var players = [];
            var fr = [];
            var g = this.game.picks;
            for (var i = g.length - 1; i >= 0; i--) {
                var p = g[i].player;
                var sd = g[i].ip;

                if(p !== 'Not picked yet'){
                    var ol = {
                        player: p,
                        ip: sd
                    }; 
                    if(fr.indexOf(p) === -1){
                        players.push(ol);
                        fr.push(p);
                    }
                }
            }
            return players;
        },

        minPaid(){
            var con1 = this.game.left_paid === 0,
            con2 = this.game.min_paid >= this.game.left_paid,
            con3 = this.game.picks_completed,
            con4 = this.game.left_free === 0;
            if((!con1 &&con2 && con3)||(!con4 &&con2 && !con3)){
                return true
            }else{
                return false
            }
        },
        miniCompleted(){
            if(this.game){
                if(this.game.picks.filter(e=>e.paid).length < this.game.picks.length){
                    return false;
                }
                return true;
            }
        },
    }
}
</script>


<style type="text/css" scoped>
    .buton {
        text-decoration: none;
        padding: 5px;
        box-shadow: 0 2px 15px rgba(0,0,0,0.16);
    }
    .add-margin {
        margin-left: -17px;
    }
    .add-margin-2 {
        margin-left: -10px;
    }
</style>




