<template>
	 <v-snackbar
      :timeout="timeout"
      :top="y === 'top'"
      :bottom="y === 'bottom'"
      :right="x === 'right'"
      :left="x === 'left'"
      :multi-line="mode === 'multi-line'"
      :vertical="mode === 'vertical'"
      v-model="snackbar"
    >
    {{showSnackbar}}

      {{ text }}
    </v-snackbar>
</template>
<script type="text/javascript">
	export default {
		props: ['text','show'],
		data(){
			return {
		        y: 'top',
		        x: null,
		        mode: '',
		        timeout: 5000,
		        snackbar: false,
			}
		},

		computed: {
			showSnackbar(){
				this.snackbar = this.show;
			}
		}
	}
</script>


