<template>
	<el-dialog
      :show-close="false"
      :visible.sync="drawer"
      width="90%"
      :before-close="closeModal" top="20px">
      <section class="stage-1 animated text-center" >
        <h3 class="text-left">Change Your Account Information</h3>

        <div class="row">
          <div class="col-lg-6 col-xs-12 form-group">
            <label class="pull-left">First Name</label>
            <el-input v-model="user.first_name"></el-input>
          </div>
          <div class="col-lg-6 col-xs-12 form-group">
            <label class="pull-left">Last Name</label>
            <el-input v-model="user.last_name"></el-input>
          </div>

          <div class="col-lg-6 col-xs-12 form-group">
            <label class="pull-left">Email</label>
            <el-input v-model="user.email"></el-input>
          </div>

          <div class="col-lg-6 col-xs-12 form-group">
            <label class="pull-left">Password</label>
            <el-input v-model="user.password" :type="'password'"></el-input>
          </div>
        </div>

      <div>
          <el-button type="default" @click="updateDetails">
            Save Changes <i class="fa fa-chevron-right"></i>
          </el-button>
        </div>
      </section>
      
  </el-dialog>
</template>

<script type="text/javascript">
  import { mapGetters, mapActions } from "vuex";

	export default {
		props: ['drawer'],
		data(){
			return {
				dwer: false,
        right: true,
        value: true,
			}
		},
		watch:{
			'drawer': {
				handler: function(old, val){
          
					this.dwer = !this.dwer;
				}
			}
		},
		computed:{
			...mapGetters('user_mgt', [
  				'user',
          'session'
  			]),
      validate(){
        var con1 = this.user.first_name.length > 0,
        con2 = this.user.first_name.length > 0,
        con3 = this.user.password.length > 0,
        con4 = this.user.email.length > 0;
        if(con1 && con2 && con3 && con4){
          return false;
        }
        return true
      }
		},
    methods: {
      updateDetails(){
        var args = {};
            args.session = this.session;
            args.user = this.user;
            var self  = this;
            this.updatePerm(args)
            .then((res)=>{
                if(res.status){
                    self.$notify({
                        type: 'success',
                        message: res.message
                    });  
                    self.closeModal();                   
                }
            });
      },
      closeModal(){
        this.$emit('close');
      },
      ...mapActions('user_mgt', ['updatePerm']),
    }
		
	}
</script>

<style type="text/css" scoped>
  .stage-1 {
    background-color: #FAFAFA;
    box-shadow: 0 20px 20px 0 rgba(0,0,0,0.1);
    padding: 20px;
    min-height: 300px;
  }
  .hesla {
    display: none;
      width:100%; 
      height:100%;
      padding-top: 110px;
  }

  .aleh {
    color: #326ADA;
    font-family: "Open Sans";
    font-size: 25px;
    font-weight: 600;
    line-height: 35px;
    text-align: center;
  }
  .project-name {
    height: 70px;
    width: 70%;
    padding: 30px;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
    margin-top: 20px;

  }
  .lan-btn {
    margin-top: 30px;
    height: 40px;
    border: transparent;
    color: #fff;
    width: 50%;
    border-radius: 50px;
    background-color: #326ADA;
    font-family: "Open Sans"; font-size: 13px;  font-weight: 600; line-height: 22px;
  }

  .type-card {
    height: 320px;
    width: 250px;
    background-color: #fff;
    cursor: pointer;
    padding: 60px 20px;
    text-align: center;
    box-shadow: 0 20px 20px 0 rgba(0,0,0,0.1);
  }
  .type-card img {
    /*width: 65px;*/
    height: 63px;
  }
  .type-card h4 {
    margin-top: 45px;
  }

  .type-card:hover .hesla {
    background-color:#FAFAFA; 
    
    cursor: pointer;
    display: block;
  }

    @media screen and (max-width: 600px) { 
    .project-name {
      width: 100%;
    }
  }
</style>



