<template>
	<el-dialog
	    fullscreen
	    :visible.sync="show"
	    :show-close="false">

	   <div class="container">
        <div class="row">
            <div class="col-xs-12 col-md-4 col-lg-4">
                
            </div>
            <div class="col-xs-12 col-md-4 col-lg-4 text-center pt-5 mt-5">
                This Game has Reached its Minimuum Paid Value, Please be patient as the Admin Concludes the game
            </div>
            <div class="col-xs-12 col-md-4 col-lg-4">
                
            </div>
        </div>

    </div>
	    
	</el-dialog>
</template>

<script type="text/javascript">
	export default {
		props: ['show']
	}
</script>


