
import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/app/landing/indexPage'
import Signup from '@/app/auth/signup'
import Login from "@/app/auth/login"
import Confirmation_sent from '@/app/auth/confirmation'
import Confirm from '@/app/auth/confirm'
import Dashboard from '@/app/admins/dashboard'
import AllGames from '@/app/admins/allGames'
import Deleted from '@/app/admins/deletedGames'
import Group from '@/app/admins/group'
import Unconfirmed from '@/app/admins/unconfirmed'
import AllAdmin from '@/app/admins/allAdmins'

import Live from '@/app/admins/live'
import DashboardLayout from '@/app/shared/layouts/dashboardLayout'
import Player from '@/app/player/player_main'


import GameConfig from '@/app/admins/newGame'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage,
      redirect: {name: 'login'}
    },
    {
      path: '/signup',
      name: 'signup',
      component: Signup
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/confirm/:id/:token',
      name: 'account-confirm',
      component: Confirm
    },
    {
      path: '/confirmation_sent',
      name: 'confirmation_sent',
      component: Confirmation_sent,
    },
    {
      path: '/players/:gameId',
      name: 'player-page',
      component: Live,
    },
    {
      path: '/users',
      name: 'dashboard',
      redirect: '/users/dashboard',
      component: DashboardLayout,
      children: [
      	{
      		path: '/users/dashboard',
	      	name: 'user-dashboard',
	      	component: Dashboard,
        },
        {
          path: '/users/group-games',
          name: 'group-games',
          component: Group,
        },
         {
          path: '/users/deleted-games',
          name: 'deleted-games',
          component: Deleted,
        },
        {
          path: '/users/pending-confirmations',
          name: 'confirm-accounts',
          component: Unconfirmed,
        },
        {
          path: '/player/dashboard/:gameId',
          name: 'player-dashboard',
          component: Player,
        },
        {
      		path: '/super-admin/games',
	      	name: 'all-games',
	      	component: AllGames,
        },
        {
      		path: '/super-admin/admins',
	      	name: 'all-admin',
	      	component: AllAdmin,
      	},
        {
          path: '/users/game-config',
          name: 'game-config',
          component: GameConfig,
        },
        {
          path: '/users/live-center/:gameId',
          name: 'live-center',
          component: Live,
        },
        
      ]
    },
  ]
})



