<template>
	 <div class="container" style="min-height: 460px;">
    <section>
      <div class="row px-4 mt-3">
          <div class="col-xs-12 col-lg-4 col-md-4">
            <label>Select Game Type</label>
             <el-select v-model="type" @change="resetState" placeholder="Select Game Type" v-validate="'required'" name="type" style="width: 90%;">
              <el-option v-for="(item, index) in $_$gameTypes"
                :key="index"
                :label="item.name"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="right col-xs-12 col-lg-4 col-md-4 visible-lg visible-md">
             <p style="font-weight: 900; font-size: 28px">Upload Game Prize</p>
          </div>  
      </div>
      <div class="row px-4 mt-3" v-if="type.length>0">
            <div class="col-xs-12 col-lg-8 col-md-8">
                <div class="row">
                  <div class="col-xs-12 col-md-6 col-lg-6">
                     <label>Total Balls</label>
                    <el-input-number v-model="game.total_balls"  class="pb-3" style="width: 90%" :min="1" 
                      :max="100" v-validate="'required'" name="total_balls" label="Total Balls"></el-input-number>
                  </div>

                  <div class="col-xs-12 col-md-6 col-lg-6" v-if="type == '3'">
                     <label>Free Balls</label>
              <el-input-number v-model="game.free_balls" name="free_balls" v-validate="'required'" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
                  </div>

                  <div class="col-xs-12 col-md-6 col-lg-6">
                     <label>Price/Ball</label>
              <el-input-number v-model="game.price" v-validate="'required'" name="price" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
                  </div>


                  <div class="col-xs-12 col-md-6 col-lg-6" v-if="type == '3'||game.type == '2'">
                    <label>Authentication Type</label>
              <el-select v-model="game.auth_type" v-validate="'required'" name="auth_type" placeholder="Select a type" class="pb-3" style="width: 90%">
                  <el-option v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                  </el-option>
              </el-select>
                  </div>



                  <div class="col-xs-12 col-md-6 col-lg-6" v-if="type == '4'">
                    <label>Total Winners</label>
              <el-input-number v-model="game.winners" name="winners" v-validate="'required'" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
                  </div>

                  <div class="col-xs-12 col-md-6 col-lg-6" v-if="type == '3'">
                     <label>Minimum Paid Numbers</label><br>
              <el-input-number v-model="game.min_paid" v-validate="'required'" name="min_paid" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
                  </div>

                 <div class="col-xs-12 col-md-6 col-lg-6">
                   <label>Winning Prize</label>
              <el-input v-model="game.prize" placeholder="" v-validate="'required'" name="prize" style="width: 90%" class="pb-3"></el-input>
                 </div>


                 <div class="col-xs-12 col-md-6 col-lg-6">
                  <label>Prize Description</label><br>
              <el-input v-model="game.prize_description" v-validate="'required'" name="prize_desc" style="width: 90%" placeholder=""></el-input>
                 </div>


                  <div class="col-xs-12 col-md-6 col-lg-6" v-if="type == '3'">
                 <el-checkbox v-model="game.others" class="pt-3 mt-4">Show on 'Other Games' List</el-checkbox>
                 </div>

                  <div class="col-xs-12 col-md-6 col-lg-6" v-if="type =='1'">
                  <label>Enter Teams</label><br>
              <el-input v-model="team" style="width: 90%" placeholder="">
                <el-button type="info" class="text-center"  slot="append" @click="add_to_team">
                  <i class="fa fa-plus"></i>
                </el-button>
              </el-input>

                 </div>

                  <div class="col-xs-12 col-md-6 col-lg-6 tems olf" v-if="type =='1'">
                   <div class="table-responsive">
                      <table class="table table-hover">
                          <tbody>
                            <tr v-for="(item, index) in Team" :key="index">
                              <td>
                                {{index+1}}
                              </td>
                              <td>
                                {{item}}
                              </td>
                            </tr>
                            <tr v-if="game.teams.length < 1">
                                <td class="el-table_3_column_8" align-center colspan="6">
                                    No Team Names Set yet
                                </td>
                            </tr>
                          </tbody>
                        </table>
                  </div>
                 </div>



                </div>
            </div>

           <div class="col-xs-12 col-lg-4 col-md-4">
             <p style="font-weight: 900; font-size: 28px" class="visible-xs">Upload Game Prize</p>
            <el-upload
              class="upload-demo" :auto-upload="false" ref="upload" :data="game"
              drag :on-success="uploadChange" :before-upload="starting2Upload"
              :action="$API+'upload-photo/'" :limit="1"
              :on-preview="handlePreview" :on-change="filesChanged"
              :on-remove="handleRemove"
              :on-error="errorHandler"
              :file-list="fileList">
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
              <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 500kb</div>
            </el-upload>
            <div class="xs12 mt-3">
                  <el-button size="small" type="primary" :loading="loading" @click="submitUpload">Complete Setup</el-button>
                  <el-button type="primary" size="small" :loading="loading" @click="closeModal">Cancel</el-button>
                  <el-button type="primary" size="small" :loading="loading" @click="shutAll">Reset</el-button>
                </div>
          </div> 
      </div>
    </section>   
  </div>
</template>
<script type="text/javascript">
  import { mapGetters, mapActions } from "vuex";
	import Types from './createGame/gameType';
  import mineConfig from './createGame/minesweeperConfig';
  import sweepConfig from './createGame/sweepstakesConfig';
  import Upload from './createGame/upload';
  import paidConfig from './createGame/paidConfig';
	export default {
		props: ['show'],
    components: {Types,
    mineConfig,
    sweepConfig,
    Upload,
    paidConfig, },

		data(){
			return {
				active: null,
        width: '100%',
        title: 'Select Game Type',
        stage: 1,
        which_one: null,
        history: [],
        teams: [],
        init: 0,
        team: '',
        frees: [],
        type: '',
        loading: false,
				dialogVisible: false,
				dialogImageUrl: '',
        fileList: [],
        files_exist: false,
        options: [
					{
						value: '1',
						label: 'Automatic'
					},
					{
						value: '2',
						label: 'Manual'
					}
				]

			}
		},
		methods: {
      ...mapActions('persistent', ['resetState','setGameInitial']),
      processType(typ) {
        this.game.type = typ.value;
        this.game.admin_name = this.user.user_full_name;
        this.game.admin_email = this.user.user_email;
        this.game.picks = [];
        console.log(this.game);
        this.goToConfig(typ.value);
      },
      submitUpload() {
        this.$validator.validateAll().then(result=>{
					if(result){
            if(this.game.type == '1'){
              if(this.game.teams.length < this.game.total_balls){
                this.$notify({
                  message: 'Team names not complete',
                  type: 'error'
                });
                return;
              }
            }
            if(this.files_exist){
              this.game.session = this.session;
              this.$refs.upload.submit();
            }else{
              this.$notify({
                message: 'You must upload a file to continue',
                type: 'error'
              });
            }
					}else{
						this.$notify({
              title: 'Some Details are Missing',
              type: 'error'
            });
					}					
				});
				
      },
     
      add_to_team(){
        
        if(this.game.total_balls > 0){
          if(this.game.teams.length < this.game.total_balls){
            if(this.game.teams.indexOf(this.team) === -1){
              this.game.teams.push(this.team);
            }
              this.team = '';
            }else{
              this.$notify({
                title: "Error Message",
                message: 'Team is Complete'
              });
            }
        }else{
          this.$notify({
            title: "Error Message",
            message: 'Total Balls Not Set'
          });
        }
      },
      uploadChange(response, file, fileList){
        if(response.status){
          this.game.prize_photo = response.file;
          this.game.Id = response.id;
          this.game.picks = [];
          this.game.winnerArray = [];
          this.game.type = this.type;

          if(this.game.free_balls){
            this.game.left_paid = Number(this.game.total_balls) - Number(this.game.free_balls);
            this.game.left_free = Number(this.game.free_balls);
            var db = [];
            for (var i = 1; i < Number(this.game.total_balls)+1; i++) {
              db.push(i)
            }
            this.game.frees = this.shuffleArray(db).slice(0, Number(this.game.free_balls));
          }else{
            this.game.left_paid = Number(this.game.total_balls);
          }

          for (let index = 1; index < this.game.total_balls+1; index++) {
            var t = {
              ball: index,
              player: 'Not picked yet',
              ip: 'Not picked yet',
              status:  'Not picked yet',
              time:  'Not picked yet',
            };
            if (this.game.type == '1') {
              t.team = this.game.teams[index-1];
            }
            this.game.picks.push(t);          
          }

          if (this.game.type == '1') {
            this.game.auth_type = '1';
              this.game.teams.length = 0;
            }
          // console.log(this.game.picks);
          this.game.auth_needed = 0;
          this.game.session = this.session;
          this.setGameInitial(this.game)
          .then((res)=>{
            this.$router.push({name: 'dashboard'})
            this.$notify({
              title: res.message,
              type: 'success'
            });
          })
            
          
      }else{
        this.$notify({
          title: response.message,
          type: 'error'
        });
      }
      },
      starting2Upload(file){
				this.loading = true;
			},
      filesChanged(file, fileList){
        this.files_exist = fileList.length > 0;
      },
			errorHandler(){
				this.$notify({
		          title: 'There was an issue uploading this file',
		          type: 'error'
		        });
		        this.loading = false;
			},
			handleRemove(){

			},
			handlePreview(file){
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
				console.log(file, this.dialogVisible, this.dialogImageUrl);
			},
      shutAll(){
        this.stage = 1;
        this.resetState();
        this.gotogametype();
      },
      shuffleArray(array) {
          for (var i = array.length - 1; i > 0; i--) {
              var j = Math.floor(Math.random() * (i + 1));
              var temp = array[i];
              array[i] = array[j];
              array[j] = temp;
          }
          return array;
      },
      completeMines(){
        if(this.game.free_balls){
          var db = [];
          for (var i = 1; i < Number(this.game.total_balls)+1; i++) {
            db.push(i)
          }
          this.frees = this.shuffleArray(db).slice(0, Number(this.game.free_balls));
        }
        this.stage = 3;
        this.title = "Upload Photo of Game Prize"
      },
      gotogametype(){
        this.stage = 1;
        this.history.push(this.stage);
        this.title = "Select a game type";
      },
      gameDone(){
        this.shutAll();
        this.$router.push({name:'live-center'});
      },
      goToConfig(type){
        switch(type){
          case '3': {
            this.title = "Configure your minesweeper game"
          }
        }


        this.stage = 2;
      },
      closeModal(done){
        this.shutAll();
        this.$router.push({name: 'user-dashboard'});
      },
      
		},
    computed: {
      ...mapGetters('persistent', ['game']),
      ...mapGetters('user_mgt', [
        'user',
        'session'
      ]),
      Team(){
        if(!this.game.teams){
          this.game.teams = [];
        }
          return this.game.teams;
      },
      initView(){
        if(this.init === 0){
          this.stage = 1;
          this.resetState();
          this.gotogametype();
          this.init = 1;
        }
      },
      

    },
    mounted(){
      if(!this.game.teams){
          this.game.teams = [];
        }
    }
	}
</script>


<style type="text/css">
  
  .el-upload__input {
    display: none !important;
  }
  .el-upload-dragger{
    width: 280px !important;
  }
</style>

<style type="text/css" scoped>
  .tems{
    height: 300px;
    overflow-y: scroll;
  }

  .olf:hover::-webkit-scrollbar{
  width: 5px;
  transition: all 1s ease;
}
.olf::-webkit-scrollbar {
  width: 0px;  
  transition: all 1s ease;
}
.olf::-webkit-scrollbar-thumb {
  border-radius: 2px !important;
   -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5) !important; 
  background-color: #848484;
  transition: all 1s ease;
  background-image: -webkit-linear-gradient(45deg, rgba(255, 255, 255, .2) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, .2) 50%, rgba(255, 255, 255, .2) 75%, transparent 75%, transparent);
}
.olf::-webkit-scrollbar-track {
  -webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.1) !important;
  border-radius: 50px;

  transition: all 1s ease;
}
</style>


