
<template>
	<div>
		<h3>Select a Game type to begin</h3>
	<v-layout row wrap class="text-center">
	      <v-flex lg3 md6 xs12 v-for="type in $_$gameTypes" :key="type.value" v-if="type.value !== '4'">
	      	<div class="people game-card mx-1">
				<div class="text-xs-center" style="margin-top: +12px;">
					<p class="stats-val">{{type.name}}</p>
					<img :src="type.img" style="height: 220px; width: 200px;">
					<p>{{type.description}}</p>
					<el-button type="info" @click="selectDis(type)">Select</el-button>
				</div>
				</div>
	      </v-flex>
	  </v-layout>
	</div>
</template>
<style type="text/css" scoped>
	.game-card {
		height: auto;
		padding: 25px 15px;
		max-width: 350px;
	}
	.game-card p.stats-val {
		text-transform: uppercase;
	}
	.game-card p{
		text-transform: capitalize;
	}
</style>

<script type="text/javascript">
	
	export default {
		data(){
			return {

			}
		},

		methods: {
			selectDis(type){
				this.$emit('selected', type);
			},
		},
		computed: {
		}

	}
</script>


