<template>
    <el-dialog 
        :visible.sync="show" width="90%" top="2vh"
        :before-close="closeModal">
        <div class="container">
            <div class="row">
                <div class="col-xs-12 col-md-6 col-lg-6">
                    <h2>Edit User</h2>
                    
                      <table class="el-table el-table--fit el-table--enable-row-hover el-table--enable-row-transition">
                                <tr class="el-table__row">
                                    <th class="el-table_3_column_8" width="50%">
                                        Permission
                                    </th>
                                    <th class="el-table_3_column_8" style="text-align: left">
                                        Status
                                    </th>
                                   
                                </tr>
                                <tr class="el-table__row" v-if="user.super">
                                   
                                    <td class="el-table_3_column_8">
                                        Mark as Super Admin
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="admin.super"
                                            active-color="#9C27B0"
                                            inactive-color="brown"/>
                                        </el-switch>
                                    </td>                      
                                </tr>

                                <tr class="el-table__row">
                                   
                                    <td class="el-table_3_column_8">
                                        Delete Games
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="permissions.delete"
                                            active-color="#9C27B0"
                                            inactive-color="brown"/>
                                        </el-switch>
                                    </td>                      
                                </tr>
                                <tr class="el-table__row">
                                   
                                    <td class="el-table_3_column_8">
                                        View All Games
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="permissions.allGames"
                                            active-color="#9C27B0"
                                            inactive-color="brown">
                                        </el-switch>
                                    </td>                      
                                </tr>
                                 <tr class="el-table__row">
                                   
                                    <td class="el-table_3_column_8">
                                        Delete All Games
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="permissions.delAllGames"
                                            active-color="#9C27B0"
                                            inactive-color="brown">
                                        </el-switch>
                                    </td>                      
                                </tr>
                                <tr class="el-table__row">
                                   
                                    <td class="el-table_3_column_8">
                                        Manage Other Admins
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="permissions.allAdmins"
                                            active-color="#9C27B0"
                                            inactive-color="brown">
                                        </el-switch>
                                    </td>                      
                                </tr>                    
                                 <tr class="el-table__row">
                                   
                                    <td class="el-table_3_column_8">
                                        View Group Games
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="permissions.groupGames"
                                            active-color="#9C27B0"
                                            inactive-color="brown">
                                        </el-switch>
                                    </td>                      
                                </tr>
                                 <tr class="el-table__row">
                                   
                                    <td class="el-table_3_column_8">
                                        Delete Group Games
                                    </td>
                                    <td class="el-table_3_column_8">
                                        <el-switch
                                            v-model="permissions.delGrpGames"
                                            active-color="#9C27B0"
                                            inactive-color="brown">
                                        </el-switch>
                                    </td>                      
                                </tr>
                                 
                            </table>
                    </div>
                    <div class="col-xs-12 col-md-6 col-lg-6">
                        <h2>Change Group</h2>
                        <el-select v-model="admin.group" style="width: 75%" placeholder="Available Groups">
                            <el-option
                                :label="'Unset'"
                                :value="''">
                            </el-option>
                            <el-option v-for="item in groups"
                                :key="item.group_name"
                                :label="item.group_name"
                                :value="item.Id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="col-xs-12 col-md-6 col-lg-6 mt-4 form-group">
                        <label>Change Password</label><br>
                        <el-input v-model="admin.password" style="width: 75%" :type="'password'">
                        </el-input>
                    </div>
            </div>
        </div>
            <span slot="footer">
                <el-button @click="closeModal" style="margin-top: 14px" size="small">Cancel</el-button>
                <el-button type="primary" style="margin-top: 14px" size="small" @click="saveChanges">Save Changes</el-button>
            </span>
    </el-dialog>
    
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    props:['show','permissions','id','admin'],
    data(){
        return{
            permissionObj: {},
        }
    },
    methods: {
        ...mapActions('persistent',['updateGame']),
        closeModal(){
            this.$emit('close',1);
        },
        saveChanges(){
            // console.log(this.permissionObj);
            
            this.$emit('savePerms', this.permissions,this.id );
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        uploadChange(response, file, fileList){
            this.game.prize_photo = response;
            var params = {
					game: this.game,
                    session: this.session,
                    id: this.id,
					frees: this.frees,
				};
				this.updateGame(params)
				.then((res)=>{
					this.loading = false;
					// this.$emit('done');
				}).catch(()=>{
					this.loading = false;
				});    
        },
			backStep(){
				this.$emit('back');
			},
			starting2Upload(file){
				this.loading = true;
			},
			errorHandler(){
				this.$notify({
		          title: 'There was an issue uploading this file',
		          type: 'error'
		        });
		        this.loading = false;
			},
			handleRemove(){

			},
			
    },
    computed: {
        ...mapGetters('user_mgt', [
				'user',
				'session',
                'groups'
            ]),
        parsePerms(){
            if(this.permissions.permissions){
                
                this.permissionObj = JSON.parse(this.permissions.permissions);
                console.log('perms',this.permissionObj);
            }else{
                this.permissionObj = this.permissions
            }
        }

    }
}
</script>




