<template>
	<div class="container">
		<section v-if="ready">
			<div class="row px-4 mt-3">
				<div class="col-xs-10">
					<h5 class="heading">Existing Admins ({{admins.length}})</h5>
				</div>
				<div class="col-xs-1">
					
				</div>
		    
			</div>
		</section>
		<section v-if="loading">
			<div class="row px-4 mt-3 text-center">
				<span>
					<i large class="blue--text lighten-2 fa fa-spinner fa-spin fa-3x"></i>
				</span>	    
			</div>
		</section>

		<div v-else>
			<div class="col-xs-12 col-md-12 col-lg-12">
		      	<el-select v-model="selected_group" style="width: 75%" placeholder="Available Groups">
		      	    <el-option
		      	        :label="'All'"
		      	        :value="'all'">
		      	    </el-option>
		      	    <el-option v-for="item in groups"
		      	        :key="item.group_name"
		      	        :label="item.group_name"
		      	        :value="item.Id">
		      	    </el-option>
		      	</el-select>
		      	<el-button type="info" @click="group_dialog = true">
		      		<i class="fa fa-plus"></i>
		      	</el-button>
		    </div>
		<section v-if="filteredAdmins.length === 0 && ready">
			<div class="row px-4 mt-3 text-center">
				<p class="pt-5 mt-5">
					<i large class="blue--text lighten-2 fa fa-folder-open-o fa-3x"></i>
					<p class="heading">No Admins Found</p>
				</p>	    
			</div>
		</section>

		<section class="pt-5" v-else>
			<div class="row px-1 mt-5">
				<div class="col-xs-12 col-md-6 col-lg-3" v-if="type.Id !== user.Id" v-for="type in filteredAdmins" :key="type.Id" >
			      	<card @refresh="fetchAdmins" @block="blocks" :admin="type"/>
			    </div>
			</div>
		</section>
		</div>

		<el-dialog
		    :visible.sync="group_dialog"
		    width="90%" :show-close="false"
		    @close="group_dialog = false">
		    
		    <div class="container">
		    	<div class="row">
		    		<label>Group Name</label><br>
		    		<el-input v-model="newGroup.group_name" placeholder=""></el-input>

		    		<el-button type="success" class="mt-3" @click="saveGroup">Create</el-button>
		    	</div>
		    </div>
		    
		</el-dialog>

	    
	    
	</div>
</template>
<style type="text/css" scoped>
	.no-games {
		text-align: center;
	}
	.wht {
		line-height: 100px;
	}
	.count {
		line-height: 20px;
		font-size: 18px;
		font-style: italic;
	}


</style>


<script type="text/javascript">
  import { mapGetters, mapActions } from "vuex";
  import card from './adminCard'

  export default {
 	data(){
 		return {
 			ready: false,
 			newGroup: {},
 			group_dialog: false,
 			selected_group: null,
 		}
 	},
 	components:{card},
 	mounted(){
 		this.selected_group = 'all'
 		this.fetchAdmins();
 	},
 	computed: {
		...mapGetters('user_mgt', [
			'user',
            'session',
            'admins',
            'loading',
            'groups'
		]),
		filteredAdmins(){
			console.log(this.admins);
			var g = this.selected_group;
			if(this.admins){
				if(g == null || g == 'all'){
					return this.admins
				}else{
					var t = this.admins.filter(e=>e.group == g);
					return t;
				}
			}else{
				return [];
			}
		},
		
		
 	},
 	methods: {
 		...mapActions('user_mgt', ['fetchAdmin','accountBlock','fetchGroups','makeGroup']),
 		fetchAdmins(){
 			var args = {
 				session: this.session,
 			}
 			this.fetchAdmin(args)
 			.then(()=>{
 				this.ready = true;
 			})
 		},

 		fetchGroup(){
 			var args = {
 				session: this.session,
 			}
 			this.fetchGroups(args)
 			.then(()=>{
 				
 			})
 		},
 		saveGroup(){
 			var args = {
 				session: this.session,
 				group: this.newGroup
 			}
 			this.makeGroup(args)
 			.then(()=>{
 				this.fetchGroup()
 			})
 		}, 		
		gameDetails(game){
			return JSON.parse(game.details);
		},
 		openGameConfig(){
 			this.$router.push({name: 'game-config'});
         },
         blocks(args){
             args.session = this.session;
             var self = this;
             this.accountBlock(args).
             then((resp)=>{
                 if(resp.status){
                     self.fetchAdmins();
                     self.$notify({
                        type: 'success',
                        message: resp.message
                    });
                 }else{
                     self.$notify({
                        type: 'error',
                        message: resp.message
                    });
                 }
             })
         }
 	}	
  }
</script>




