<template>
	<div class="container">
		<div class="row">
			<div class="col-xs-12 col-md-4 col-lg-3"></div>
			<div class="col-xs-12 col-md-4 col-lg-6 people text-center">
				<img :src="require('@/assets/lottery.png')" style="width: 136px;" alt="avatar">

				<form class="row text-center">
					<div class="col-md-1 col-lg-2"></div>
					<div class="col-xs-12 col-md-10 col-lg-8">
						<label class="mt-2 left">Email</label>
					    <el-input
					      v-model="email"
					      label="E-mail"
					      required class="mt-2 mb-2"
					    ></el-input>
					    <label class="mt-2 left">Password</label>
					    <el-input
					       v-model="password"
					      label="Enter your password"
					      min="8" class="mt-2 mb-2"
					      value="" clearable
					      error required
					      :type="'password'"
					    ></el-input>
					     <el-checkbox class="left" v-model="checkbox">Remember Me from this Device?</el-checkbox>
					</div>
				</form>
				<div class="row text-center mt-4 mb-5">
					<el-button :loading="loading" :disabled="loading" color="success" class="deep-purple white--text" @click="LoginBtn">Login</el-button>
				</div>
			</div>
			<div class="col-xs-12 col-md-4 col-lg-3"></div>
		</div>
	</div>
</template>

<script type="text/javascript">
import { mapGetters, mapActions } from "vuex";
  import Notify from '@/app/shared/notify'
	export default {
		props: ['show'],
		components:{Notify},
		data(){
			return {
			  valid: false,
			  dialog: false,
			  value: true,
			  checkbox: true,
		      password: '',
		      email: '',
		      emailRules: [
		        v => !!v || 'E-mail is required',
		        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
		      ],
			}
		},
		watch:{
			'show': {
				handler: function(old, val){
					this.clear();
				}
			}
		},
		mounted(){
			if(this.session){
				this.$router.push({name:'dashboard'})
			}
		},
		computed: {
			...mapGetters('auth', ['loading','autherror','autherrorMsg']),
			...mapGetters('user_mgt', [
  				'user',
          'session',
          'perms'
  			]),
		},
		methods:{
			closeLogin(){
				this.$emit('close');
			},
			 ...mapActions('user_mgt', [
          'logout_user',
          'refreP'
        ]),
			refreshPerms(){
         if(this.session){
            var args = {};
            var self = this;
            args.Id = this.user.user_id;
            args.session = this.session;
            this.refreP(args).
            then((res)=>{
            });
         }
        },
			 ...mapActions('auth',[
		        'login',
		        'resetState'
		        ]),
			LoginBtn(){
				 var args = {
				 	"guard":"admin",
				 	"email":this.email,
				 	"password":this.password
				 };
				 var self = this;
		        this.login(args).then((response)=>{
		        	if(response.status){
								self.refreshPerms();
		        		self.$router.push({name: 'dashboard'});
		        	}
		        	if(!response.status){
		        		self.$notify({
				          title: 'Login Failed',
				          message: self.autherrorMsg,
				          type: 'error'
				        });
		        		setTimeout(()=>{
			        		self.resetState();
			        	}, 6000);
		        	}
		        	// console.log(response);
		        });
		        
			},
		}
	}
</script>

<style type="text/css">
	.login-card {
		height: 100%;
		padding: 15px;
		width: auto;
	}
</style>


