<template>
    <div class="people game-card mx-1">
				<div class="text-xs-center" style="margin-top: +12px;">
					<p class="stats-val">
						{{details.prize}}
						<el-badge :value="'Deleted'" v-if="!details.active">
						    
						</el-badge>
					</p>			
                    <p>
                        <img :src="$API+'/contents/'+details.prize_photo" style="height: 220px; width: 90%;">
                    </p>
					<p>
						<!-- <b>[{{$_$gameTypes.find(e=> e.value === details.type).name}}]</b> -->
					</p>
                    <p>
                        Remaining Paid: <b>{{details.left_paid||0}}</b>&nbsp&nbsp
						Free: <b>{{details.left_free||0}}</b><br>
						Outstanding Authentications: <b>{{details.auth_needed||0}}</b><br>
						Game Type: <b>{{$_$gameTypes.find(e=>e.value==details.type).name}}</b>
                    </p>
                    <p>
						<el-tooltip content="Delete Game" v-if="delPerm && details.active" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="deleteGame">
								<i class="fa fa-trash-o"></i>
							</el-button>
						</el-tooltip>
						
						<el-tooltip content="Live Center" v-if="details.active" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="goLive">
								<i class="fa fa-bolt"></i>
							</el-button>
						</el-tooltip>
						
						<el-tooltip content="Share This Game" v-if="details.active" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="previewGame">
								<i class="fa fa-share-square-o"></i>
							</el-button>
						</el-tooltip>
						
						<el-tooltip content="Edit This Game" v-if="details.active" placement="bottom" effect="dark">
							<el-button type="info" size="small" @click="editGame">
								<i class="fa fa-edit"></i>
							</el-button>
						</el-tooltip>
						
                        
					</p>
				</div>

				<Editor :show="edit" @close="closeEdit" :id="game" :game="details"/>
				<Preview :show="preview" @close="closeEdit" :id="game" :game="details"/>
				</div>
</template>

<script>
import Editor from "./editGameModual";
import Preview from "./previewGame";
  import { mapGetters, mapActions } from "vuex";

export default {
	props:['details', 'game'],
	components: {Editor, Preview},
	data(){
		return {
			edit: false,
			preview: false,
			currentGame: {},
			permissionObj: {},
		}
	},
	methods: {
		...mapActions('games',['adminGames','deleteGames']),
		editGame(){
			this.edit = true;
		},
		closeEdit(loop){
			if(loop == 1){
				this.$emit('refresh');
			}
			this.edit = false;
			this.preview = false;
		},
		previewGame(){
			this.preview = true;
		},
		deleteGame(){
			var self = this;
			this.$confirm('Are you sure you delete this Game?', 'Warning', {
                confirmButtonText: 'Yes, I\'m sure',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(()=>{
                var old = self.details.prize_photo;
				var args = {
					id: self.game,
					session: self.session,
					photo: old
				};
				self.deleteGames(args)
				.then(result=>{
					self.fetchGames();
				})
            }).catch(()=>{

            });
			
		},
		goLive(){
			this.$router.push({name: 'live-center', params: {gameId: this.game}});
		},
		...mapActions('games',['adminGames']),
 		fetchGames(){
 			var self = this;
 			var args = {
 				Id: this.user.Id,
 				session: this.session,
 			}
 			this.adminGames(args)
 			.then(()=>{
 				self.ready = true; 
 			})
 		},

	},
	computed: {
		...mapGetters('user_mgt', [
			'user',
			'session',
			'perms'
		]),
		...mapGetters('games', [
			'live_game',
			'loading',
			'mini_games',

		]),

		delPerm(){
			return this.user.permissions.delete;
		},
		parsePerms(){
            if(this.session){
				if(this.perms){
              this.permissionObj = JSON.parse(this.perms.permissions);
            }else{
              this.permissionObj = this.perms
            }
			}
        }
	},

}
</script>

<style type="text/css" scoped>
	.game-card {
		height: auto;
		padding: 25px 15px;
		max-width: 350px;
	}
	.game-card p.stats-val {
		text-transform: uppercase;
	}
	.game-card p{
		text-transform: capitalize;
	}
</style>


