<template>
    <el-dialog
        title="Share Game"
        :visible.sync="show"
        width="100%" :before-close="closeModal"
        @close="closeModal">
        <el-input id="copyId"
            :value="setUrl">
                <el-button slot="append" class="copy-icon" @click="copyfieldvalue('evt', 'copyId')">
                <i class="fa fa-copy copy-btn"></i>
                </el-button>
        </el-input>
        <div class="fa fa-checked px-2 label label-success my-2" v-if="copied">Copied</div>
        <span slot="footer">
            <div class="apo"></div>
            <el-button @click="closeModal" size="small">Close</el-button>
        </span>
    </el-dialog>
    
</template>

<script>
export default {
    props:['show','game','id'],
    data(){
        return{
            copied: false,
        }
    },
    methods: {
        closeModal(){
            this.$emit('close',0);
        },
        saveChanges(){

        },
        copyfieldvalue(e, id){
            var field = document.getElementById(id)
            field.focus()
            field.setSelectionRange(0, field.value.length)
            var copysuccess = this.copySelectionText()
            if (copysuccess){
                $('.copy-btn').removeClass('fa-copy');
                $('.copy-btn').addClass('fa-check');
                $('.copy-icon').addClass('alert-success');
                this.copied = true;
                var self = this;
                // $('#fb').html('<span class="fa fa-checked">Copied</span>');
                setTimeout(function () {
                    $('.copy-btn').removeClass('fa-check');
                    $('.copy-btn').addClass('fa-copy');
                    self.copied = false;
                }, 3000);
            }
        },
        copySelectionText(){
            var copysuccess // var to check whether execCommand successfully executed
            try{
                copysuccess = document.execCommand("copy") // run command to copy selected text to clipboard
            } catch(e){
                copysuccess = false
            }
            return copysuccess
        }
    },
    computed: {
        setUrl(){
        	var url = location.origin+'/player/dashboard/'+this.id;
        	return url;
        },
    }
}
</script>
<style scoped>
.copy-icon {
    font-size: 18px;
}
</style>





