<template>
    <nav class="navbar navbar-default bg-base-6 navbar-fixed-top" style="border: none; padding-left: 70px; padding-right: 20px; padding-top: 10px;padding-bottom: 10px;">
  <div class="">
    <!-- Brand and toggle get grouped for better mobile display -->
    <div class="navbar-header">
      <div class="text-center">
       <router-link class="navbar-brand" :to="{name: 'HomePage'}"><img src="../../assets/Nester Global Solutions Logos-02.png" class="logo" > </router-link>
      </div>
    </div>
  </div><!-- /.container-fluid -->


</nav>



</template>
<script>
  import vueinterval from 'vue-interval/dist/VueInterval.common'
  import { mapGetters, mapActions } from "vuex";
export default {
    data(){
        return {
            menu: false,
        }
    },
    methods: {
        isActive(page){
            if(page === this.$route.name){
                console.log(this.$route.name);
                return true;
            }
        },
        goLogin(){
            this.$router.push({name:'login'})
        },
        goSignup(){
            this.$router.push({name:'signup'})
        },
        openMenu(){
            this.$emit('menu');
        },
        goMenu(name){
            this.$router.push({name: name});
            this.menu = false;
        }
    },
    computed: {
        ...mapGetters('user_mgt', [
           'user',
          'session',
          'perms',
          'isAuth'
        ]),
    }
}
</script>


<style scoped>
form.pos-add {
      position: relative;
    top: 37px;
    right: 40px;
}
.navbar-brand>img {
    bottom: 40px;
}
.navbar-default .navbar-nav>li>a {
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 14px;
    color: #FFFFFF  !important;
}
.nav {
  margin-top: 30px;
}
.finAct {
    font-weight: 700 !important;
    font-size: 17px !important;
}
.links a,.navbar-brand {
    line-height: 40px;
    height: 100% !important;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.88px;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff !important;
    text-align: center !important;
    font-weight: 500;
    padding: unset !important;
}
.log{
    background: #1b0f00;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 10px 25px;
    margin-top: 15px;
}
.sign{
    background: #d4af3f;
    margin-top: 15px;
    margin-left: 18px;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 8px 35px;
}
.sign:hover, .sign:focus, .sign:active{
    background: #d4af3f;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    box-shadow: 0 10px 40px 0 rgba(212,175,63,0.2);
}
.log:hover, .log:focus, .log:active{
    background: #1b0f00;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    box-shadow: 0 10px 40px 0 rgba(212,175,63,0.2);
}
.navbar-toggle:hover,.navbar-toggle:focus,.navbar-toggle:active{
    background-color: transparent !important;
}
.navbar-toggle {
    height: 65% !important;
    text-align: center;
    font-size: 30px;
}
.hol{
    color: transparent;
}
.btns {
    margin-top: 15px;
}
.collapse {
    margin-left: -35px;
}
.navbar-brand{
    color: #d4af3f !important;
    font-size: 24px;
    letter-spacing: 2.86px;
    margin: auto;
    margin-left: -30px;
    font-weight: bold;
}
.hoila {
    line-height: 10px;
}
.lopp{
    line-height: 45px;
}
.active2 {
    background-color: darkslategray !important;
}

</style>







