<template>
	<v-container>
	  <h2>Upload Game Prize</h2>

	<el-upload
	  class="upload-demo" :auto-upload="false" ref="upload"
	  drag :on-success="uploadChange" :before-upload="starting2Upload"
	  :action="$API+'games/upload'" :limit="1"
	  :on-preview="handlePreview"
	  :on-remove="handleRemove"
	  :on-error="errorHandler"
	  :file-list="fileList">
	  <i class="el-icon-upload"></i>
	  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
	  <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 500kb</div>
	</el-upload>
	<v-flex xs8 mt-3>
      	<el-button size="small" :disabled="!game.validated" type="primary" @click="submitUpload">Complete Setup</el-button>
      </v-flex>
	<el-dialog :visible.sync="dialogVisible" width="300px">
	  <img width="100%" :src="dialogImageUrl" alt="">
	</el-dialog>
	<spinner :show.sync="loading"/>
	</v-container>
</template>

<script type="text/javascript">
	import { mapGetters, mapActions } from "vuex";
	export default {
		props:['frees'],
		data(){
			return {
				loading: false,
				dialogVisible: false,
				dialogImageUrl: '',
				fileList: [],
			}
		},
		methods: {
			submitUpload() {
				this.$refs.upload.submit();
			},
			...mapActions('persistent',['setGameInitial']),
			uploadChange(response, file, fileList){
				this.game.prize_photo = response;
				var params = {
					game: this.game,
					owner_id: this.user.user_id,
					session: this.session,
					frees: this.frees,
				};
				this.setGameInitial(params)
				.then((res)=>{
					this.loading = false;
					this.$emit('done');
				}).catch(()=>{
					this.loading = false;
				});
			},
			backStep(){
				this.$emit('back');
			},
			starting2Upload(file){
				this.loading = true;
			},
			errorHandler(){
				this.$notify({
		          title: 'There was an issue uploading this file',
		          type: 'error'
		        });
		        this.loading = false;
			},
			handleRemove(){

			},
			handlePreview(file){
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
				console.log(file, this.dialogVisible, this.dialogImageUrl);
			}

		},
		computed: {
			...mapGetters('persistent', ['game']),
			...mapGetters('user_mgt', [
				'user',
				'session'
			]),
		}
	}
</script>


