<template>
    <div>   
        <PNav v-if="routeName == 'player-dashboard'"/>
        <Nav @menu="openMenu" @side="openEdit" v-else/>
        <router-view style="margin-top: 80px !important; min-height: 640px !important"/>
        <Footer/>
        <el-dialog v-if="menu"
        :visible.sync="menu"
        width="90%"
        @close="menu = false" :show-close="false">
          <ul class="nav navbar-nav bg-base-2 menue alpha-9">
            <li class="links" >
                <a href="#" :class="{'finAct': isActive('HomePage')}" 
                @click.prevent="goMenu('HomePage')">Home</a>
            </li>
            <li class="links"  v-if="isAuth && (user.permissions.allAdmin||user.super) ">
                <a href="#" :class="{'finAct': isActive('all-admin')}" 
                @click.prevent="goMenu('all-admin')">Admin Board</a>
            </li>
            <li class="links"  v-if="isAuth && (user.permissions.allGames || user.super)">
                <a href="#" :class="{'finAct': isActive('all-games')}" 
                @click.prevent="goMenu('all-games')">All Games</a>
            </li>
            <li class="links"  v-if="isAuth">
                <a href="#" :class="{'finAct': isActive('user-dashboard')}" 
                @click.prevent="goMenu('user-dashboard')">Dashboard</a>
            </li>
             <li class="links"  v-if="isAuth && user.permissions.groupGames && user.group">
                <a href="#" :class="{'finAct': isActive('group-games')}" 
                @click.prevent="goMenu('group-games')">Group Games</a>
            </li>
             <li class="links"  v-if="isAuth && user.super">
                <a href="#" :class="{'finAct': isActive('confirm-accounts')}" 
                @click.prevent="goMenu('confirm-accounts')">Pending Confirmations</a>
            </li>
             <li class="links" @click="openEdit" v-if="isAuth">
                <a href="#"
                @click.prevent="openEdit">Profile</a>
            </li>
            <li class="links" v-if="isAuth">
                <a href="#" 
                @click.prevent="goMenu('logout')">logout</a>
            </li>
             <li v-if="!isAuth" class="nav-item divider hidden-xs"> 
            </li>
            <li v-if="!isAuth" class="links">
                <a href="#" @click.prevent="goMenu('login')">Log in</a>
            </li>
           <form v-if="!isAuth" class="form-inline my-2 my-md-0 navbar-right text-center" >
            <a href="#" @click.prevent="goMenu('signup')" class="btn mr-sm-2 my-sm-0 sign btn-circle">get started</a>
            
        </form>
          </ul>
    </el-dialog>
      <sideBar :drawer="ch_pwd" @close="closeEdit"/>
    </div>
</template>

<script>
    import sideBar from '@/app/shared/editProfile'
import Nav from '@/app/landing/nav'
import PNav from '@/app/shared/playerNav'
import Footer from '@/app/landing/footer'
import Home from '@/app/landing/indexPage'
import vueinterval from 'vue-interval/dist/VueInterval.common'
import { mapGetters, mapActions } from "vuex";
export default {
    components: {Nav, Footer, Home, PNav,sideBar},
    data(){
      return {
        menu: false,
        ch_pwd: false,
      }
    },
    methods: {
        ...mapActions('user_mgt', ['logout_user']),
      openMenu(){
        this.menu = true;
      },
      logout(){
        this.logout_user();
      },
      openEdit(){
        this.menu = false;
        this.ch_pwd = true;
      },
      closeEdit(){
        this.ch_pwd = false;
      },
       isActive(page){
            if(page === this.$route.name){
                console.log(this.$route.name);
                return true;
            }
        },
        goMenu(name){
            if(name === 'logout'){
                this.logout();
                this.$router.push({name: 'login'});
                this.menu = false;
                return;
            }else{
                this.$router.push({name: name});
                this.menu = false;
            }
        }
    },
    computed: {
        ...mapGetters('user_mgt', [
           'user',
          'session',
          'perms',
          'isAuth'
        ]),
        routeName(){
            return this.$route.name;
        }
    }
}
</script>
<style type="text/css">
    .v-modal {
        background-color:#031d5b !important;color:#FFF !important
    }
</style>



<style scoped>
.menue {
  margin-top: -60px;
  margin-left: -20px;
  width: 114%;
  margin-bottom: -30px;
    padding-bottom: 10px;
}
.finAct {
    font-weight: 700 !important;
    font-size: 17px !important;
    color: #fff !important;
}
.links a,.navbar-brand {
    line-height: 40px;
    height: 100% !important;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.88px;
    font-size: 14px;
    text-transform: uppercase;
    color: grey;
    text-align: center !important;
    font-weight: 500;
}
.links a:hover{
  color: #031d5b !important;
}
.log{
    background: #1b0f00;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 10px 25px;
    margin-top: 15px;
}
.sign{
    background: #d4af3f;
    margin-top: 15px;
    margin-left: 18px;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 8px 35px;
}
.sign:hover, .sign:focus, .sign:active{
    background: #d4af3f;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    box-shadow: 0 10px 40px 0 rgba(212,175,63,0.2);
}
.log:hover, .log:focus, .log:active{
    background: #1b0f00;
    color: #031d5b;
    text-transform: uppercase;
    font-size: 12px;
    box-shadow: 0 10px 40px 0 rgba(212,175,63,0.2);
}
.navbar-toggle:hover,.navbar-toggle:focus,.navbar-toggle:active{
    background-color: transparent !important;
}
.navbar-toggle {
    height: 65% !important;
    text-align: center;
    font-size: 30px;
}
.hol{
    color: transparent;
}
.btns {
    margin-top: 15px;
}
.collapse {
    margin-left: -35px;
}
.navbar-brand{
    color: #d4af3f !important;
    font-size: 24px;
    letter-spacing: 2.86px;
    margin: auto;
    line-height: 45px;
    font-weight: bold;
}
.active2 {
    background-color: darkslategray !important;
}

</style>



