<template>
    <el-dialog
        title="Edit Game" 
        :visible.sync="show" width="100%"
        :before-close="closeModal">
        <v-flex offset-lg2 offset-md2 xs12 lg8 md8>
            <el-collapse v-model="activeItems" :accordion="true">
                <el-collapse-item :name="details" title="Change Game Details">
                    <v-layout row wrap>
                        <v-flex lg6 md6 xs12 class="px-4">
                            <p>
                <label>Total Balls</label><br/>
              <el-input-number disabled v-model="game.total_balls"  class="pb-3" style="width: 90%" :min="1" 
            :max="100" v-validate="'required'" name="total_balls" label="Total Balls"></el-input-number>
              </p>
              
              <p v-if="game.type == '3'">
                <label>Free Balls</label><br/>
              <el-input-number disabled v-model="game.free_balls" name="free_balls" v-validate="'required'" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
              </p>
              <p>
                <label>Price/Ball</label><br/>
              <el-input-number disabled v-model="game.price" v-validate="'required'" name="price" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
              </p>
              <p v-if="game.type == '3'">
                <label>Authentication Type</label><br/>
              <el-select v-model="game.auth_type" v-validate="'required'" name="auth_type" placeholder="Select a type" class="pb-3" style="width: 90%">
                  <el-option v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                  </el-option>
              </el-select>
              </p>
               <p v-if="game.type !== '3'">
                <label>Number of Winners</label><br/>
                <el-input-number v-model="game.winners" style="width: 90%" disabled :min="1" :max="10" label=""></el-input-number>
              </p>
            </v-flex>
            <v-flex lg6 md6 xs12  class="px-4">
              <p v-if="game.type == '3'">
                <label>Minimum Paid Numbers</label><br>
              <el-input-number disabled v-model="game.min_paid" v-validate="'required'" name="min_paid" class="pb-3" style="width: 90%" :min="1" :max="100" label="Total Balls"></el-input-number>
              </p>
              <p>
                <label>Winning Prize</label>
              <el-input v-model="game.prize" placeholder="" v-validate="'required'" name="prize" style="width: 90%" class="pb-3"></el-input>
              </p>
              <p>
                <label>Prize Description</label><br>
              <el-input v-model="game.prize_description" v-validate="'required'" name="prize_desc" style="width: 90%" placeholder=""></el-input>
              </p>
              <p v-if="game.type !== '4'">
                <el-checkbox v-model="game.others" class="pt-3 mt-4">Show on 'Other Games' List</el-checkbox>
              </p>
                        </v-flex>
                    </v-layout>
                </el-collapse-item>
                <el-collapse-item title="Change Prize Photo" :name="photo">
                    <v-flex offset-xs2 class="px-1">
                        <el-upload 
                            class="upload-demo"
                            :on-success="uploadChange"
                            :action="$API+'games/change_upload/'+game.prize_photo+'/'+game.Id" :auto-upload="false" :data="{session: session}"
                            :limit="1" ref="upload"
                            :on-remove="handleRemove"
                            :on-error="errorHandler">
                            <el-button slot="trigger" size="small" type="primary">select file</el-button>
                            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">upload to server</el-button>
                            <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 500kb</div>
                        </el-upload>
                    </v-flex>
                </el-collapse-item>
                
            </el-collapse>

            
            <span slot="footer">
                <el-button @click="closeModal" style="margin-top: 14px" size="small">Cancel</el-button>
                <el-button type="primary" style="margin-top: 14px" size="small" @click="saveChanges">Save Changes</el-button>
            </span>
        </v-flex>
    </el-dialog>
    
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
    props:['show','game','id'],
    data(){
        return{
            options: [
					{
						value: '1',
						label: 'Automatic'
					},
					{
						value: '2',
						label: 'Manual'
					}
                ],
                details: '1',
                photo: '2',
                activeItems: null
        }
    },
    methods: {
        ...mapActions('persistent',['updateGame']),
        closeModal(){
            this.$emit('close',1);
        },
        saveChanges(){
            var params = {
    					game: this.game,
              session: this.session,
              id: this.id,
            };
				this.updateGame(params)
				.then((res)=>{
          this.loading = false;
					this.closeModal();
				}).catch(()=>{
					this.loading = false;
				});
        },
        submitUpload() {
            this.$refs.upload.submit();
        },
        uploadChange(response, file, fileList){
            if(response.status){
              this.game.prize_photo = response.result;
                  var params = {
                game: this.game,
                          session: this.session,
                          id: this.id,
                frees: this.frees,
              };
              this.updateGame(params)
              .then((res)=>{
                this.loading = false;
                // this.$emit('done');
              }).catch(()=>{
                this.loading = false;
              });
            }else{
               this.$notify({
                title: response.message,
                type: 'error'
              });
            }    
        },
			backStep(){
				this.$emit('back');
			},
			starting2Upload(file){
				this.loading = true;
			},
			errorHandler(){
				this.$notify({
		          title: 'There was an issue uploading this file',
		          type: 'error'
		        });
		        this.loading = false;
			},
			handleRemove(){

			},
			
    },
    computed: {
        ...mapGetters('user_mgt', [
				'user',
				'session'
			]),

    }
}
</script>




