// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {API} from './config'
import store from './store'
import spinner from '@/app/shared/spinner'
import GlobalComponents from './app/globalComponents'
import VeeValidate from 'vee-validate';
import Vuetify from 'vuetify';

require('./assets/css/bootstrap/css/bootstrap.css')
require('./assets/assets/css/boomerang-startup-analitycs.min.css')
require('./assets/assets/css/custom-style.css')
import './assets/assets/css/vuetify.css';

import ElementUI from 'element-ui'

import locale from 'element-ui/lib/locale/lang/en'

import 'element-ui/lib/theme-chalk/index.css'

Vue.use(ElementUI, { locale })
Vue.use(GlobalComponents)
Vue.use(VeeValidate);
Vue.use(Vuetify);



// import Vuetify from 'vuetify';
require('@/assets/css/style.css');
// require('@/assets/css/bootstrap/css/bootstrap.min.css');
require('@/assets/css/font-awesome.css');


// Vue.use(Vuetify, {
//   theme: {
//     primary: '#212121',
//     secondary: '#616161',
//     tertiary: '#BDBDBD'
//   }
// });
Vue.component('Spinner', spinner)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



