<template>
    <nav class="navbar navbar-default bg-base-2 navbar-fixed-top" style="border: none; padding-left: 70px; padding-right: 40px; padding-top: 10px;padding-bottom: 10px;">
  <div class="">
    <!-- Brand and toggle get grouped for better mobile display -->
    <div class="navbar-header">
      <button type="button" class="btn bg-base-2 pull-right visible-xs px-3 pt-4" @click="openMenu" >
        <i class="fa fa-bars fa-2x"></i>
      </button>
      <div class="text-center">
       <router-link class="navbar-brand" :to="{name: 'HomePage'}"><img src="../../assets/Nester Global Solutions Logos-02.png" class="logo" > </router-link>
      </div>
    </div>



    <!-- Collect the nav links, forms, and other content for toggling -->
    <div class="hidden-xs bg-base-2 alpha-9">
      <ul class="nav navbar-nav navbar-right" v-if="isAuth">
            <li class="links" v-if="user.permissions.allAdmins || user.super">
            <router-link :class="{'finAct': isActive('all-admin')}" :to="{name: 'all-admin'}">Admins Board</router-link>
        </li>
        <li class="links" v-if="user.permissions.allGames|| user.super">
            <router-link :class="{'finAct': isActive('all-games')}" :to="{name: 'all-games'}">All Games</router-link>
        </li>
         <li class="links" v-if="user.permissions.allGames || user.super">
            <router-link :class="{'finAct': isActive('deleted-games')}" :to="{name: 'deleted-games'}">Deleted Games</router-link>
        </li>
        <li class="links">
            <router-link :class="{'finAct': isActive('user-dashboard')}" :to="{name: 'user-dashboard'}">Dashboard</router-link>
        </li>
        <li class="links" v-if="user.super">
            <router-link :class="{'finAct': isActive('confirm-accounts')}" :to="{name: 'confirm-accounts'}">Pending Confirmations</router-link>
        </li>

         <li class="links" v-if="user.permissions.groupGames && user.group">
            <router-link :class="{'finAct': isActive('group-games')}" :to="{name: 'group-games'}">Group Games</router-link>
        </li>

        <li class="links" v-if="isAuth">
                <el-dropdown trigger="click" @command="handleCommand" circle style="border: 1px solid #fff; border-radius: 100%; width: 40px; height: 40px;color: #fff; top: -16px; padding-top: 7px; padding-right: 20px; padding-left: 15px;">
                    <span> 
                        <i class="fa fa-user"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="1">
                            Edit Profile
                        </el-dropdown-item>
                        <el-dropdown-item command="2">
                            Logout
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
        </li>
        
      </ul>
      <ul class="nav navbar-nav navbar-right" v-else>
        <li class="links">
            <router-link :class="{'finAct': isActive('login')}" :to="{name: 'login'}">Login</router-link>
        </li>
        <li class="links">
             <router-link :class="{'finAct': isActive('signup')}" :to="{name: 'signup'}">get started</router-link>
        </li>
      </ul>
    </div><!-- /.navbar-collapse -->
  </div><!-- /.container-fluid -->




</nav>



</template>
<script>
  import vueinterval from 'vue-interval/dist/VueInterval.common'
  import { mapGetters, mapActions } from "vuex";
export default {
    components: {},
    data(){
        return {
            menu: false,
            ch_pwd: false,
        }
    },
    methods: {
        ...mapActions('user_mgt', ['logout_user']),
        isActive(page){
            if(page === this.$route.name){
                console.log(this.$route.name);
                return true;
            }
        },
        handleCommand(command){
            if(command == '1'){
                this.$emit('side')
            }
            if(command == '2'){
                this.logout();
            }

        },
        goLogin(){
            this.$router.push({name:'login'})
        },
        goSignup(){
            this.$router.push({name:'signup'})
        },
        openMenu(){
            this.$emit('menu');
        },
        goMenu(name){
            this.$router.push({name: name});
            this.menu = false;
        },
        logout(){
            this.logout_user();
            this.$router.push({name: 'login'})
        }
    },
    computed: {
        ...mapGetters('user_mgt', [
           'user',
          'session',
          'perms',
          'isAuth'
        ]),
    }
}
</script>


<style scoped>
form.pos-add {
      position: relative;
    top: 37px;
    right: 40px;
}
.navbar-brand>img {
    bottom: 40px;
}
.navbar-default .navbar-nav>li>a {
    font-family: Nunito Sans;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 14px;
    color: grey;
}
.nav {
  margin-top: 30px;
}
.finAct {
    color: #fff !important;
}
.links a,.navbar-brand {
    line-height: 40px;
    height: 100% !important;
    text-align: center;
    text-decoration: none;
    letter-spacing: 0.88px;
    font-size: 12px !important;
    text-transform: uppercase;
    color: #fff;
    text-align: center !important;
    font-weight: 100 !important;
    padding: unset !important;
    margin: auto 10px;
}
.log{
    background: #1b0f00;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 10px 25px;
    margin-top: 15px;
}
.sign{
    background: #d4af3f;
    margin-top: 15px;
    margin-left: 18px;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    padding: 8px 35px;
}
.sign:hover, .sign:focus, .sign:active{
    background: #d4af3f;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    box-shadow: 0 10px 40px 0 rgba(212,175,63,0.2);
}
.log:hover, .log:focus, .log:active{
    background: #1b0f00;
    color: #fff;
    text-transform: uppercase;
    font-size: 12px;
    box-shadow: 0 10px 40px 0 rgba(212,175,63,0.2);
}
.navbar-toggle:hover,.navbar-toggle:focus,.navbar-toggle:active{
    background-color: transparent !important;
}
.navbar-toggle {
    height: 65% !important;
    text-align: center;
    font-size: 30px;
}
.hol{
    color: transparent;
}
.btns {
    margin-top: 15px;
}
.collapse {
    margin-left: -35px;
}
.navbar-brand{
    color: #d4af3f !important;
    font-size: 24px;
    letter-spacing: 2.86px;
    margin: auto;
    margin-left: -30px;
    font-weight: bold;
}
.hoila {
    line-height: 10px;
}
.lopp{
    line-height: 45px;
}
.active2 {
    background-color: darkslategray !important;
}

</style>







