<template>
     <div class="container">
         <h2>Players Center</h2>
         <div class="row" v-if="user.registered">
             <p style="font-size: 14px; font-weight: 600; line-height: 14px;" class="ml-3">{{user.first_name+' '+user.last_name}}
                <el-button type="outline" @click="logout">Logout</el-button>
             </p>
         </div>
        <div class="row">
            <div class="col-xs-12 col-md-1 col-lg-1" v-if="user.registered">
                <div class="row">
                    <div class="col-xs-3 col-lg-12">
                         <v-btn :class="getColor('Not picked yet')" style="width: 100%" class="white--text" dark small>
                            unpicked
                        </v-btn>
                    </div>

                    <div class="col-xs-3 col-lg-12">
                        <v-btn :class="getColor('paid')" style="width: 100%" class="white--text" dark small>
                            Paid
                        </v-btn>
                    </div>

                    <div class="col-xs-3 col-lg-12">
                        <v-btn :class="getColor('free')" style="width: 100%" class="white--text" dark small>
                            Free
                        </v-btn>
                    </div>
                     
                </div>
            </div>
            <div class="col-xs-12 col-md-7 col-lg-7" v-if="user.registered">
                <div class="table-responsive">
                    <table class="table table-hover">
                        <thead>
                          <tr>
                            <th>
                                Balls
                            </th>
                            <th >
                                Player
                            </th>
                            <th v-if="game.type == '1'">
                                Teams
                            </th>
                          </tr>
                        </thead>
                        <tbody v-if="game">
                          <tr v-for="(item, index) in game.picks" :key="index" style="padding-top: 10px;">
                            <td class="el-table_3_column_8" width="70px">
                                <el-button style="width: 40px; height: 40px;" circle :disabled="disable(item.status)" :class="getColor(item.status)" @click="picker(index)" class="white--text" dark small> 
                                    {{item.ball}}
                                    <v-badge :color="getColor(item.status)" v-if="item.paid" :class="{'add-margin':Number(item.ball)>9}">
                                        <i slot="badge" class="fa fa-paypal"></i>
                                    </v-badge>
                                    </el-button>
                            </td>
                            <td>
                                <el-badge :value="isWinner(item.ball)" class="item">
                                    {{item.player}}<br>
                                    <span v-if="item.player !=='Not picked yet'">{{getDate(item.time)}}
                                    </span>
                                </el-badge>
                            </td>
                            <td v-if="game.type == '1'">
                                {{game.game_completed?item.team:'< Hidden >'}}
                            </td>
                        </tr>
                        <tr v-if="game.picks.length < 1">
                            <td class="el-table_3_column_8" align-center colspan="6">
                                No Data found for this game
                            </td>
                        </tr>
                        </tbody>
                      </table>
                </div>
            </div>
             <div class="col-xs-12 col-md-8 col-lg-8" v-else>
                  <h4 align-center>
                    It seems you are new Here. Enter Your Name Please
                </h4>

                <p>
                    <label>First Name</label><br>
                    <el-input v-model="user.first_name" v-validate="'required'" name="first_name" class="pb-3" style="width: 90%"></el-input>
                </p>
                <p>
                    <label>Last Name</label><br>
                    <el-input v-model="user.last_name" v-validate="'required'" name="last_name" class="pb-3" style="width: 90%"></el-input>
                </p>
                <p>
                    <v-btn color="teal darken-4 white--text" @click="register">Submit</v-btn>
                </p>
            </div>
            <div class="col-xs-12 col-md-4 col-lg-4">
                <h3>Game Details</h3>
                <div class="table-responsive">
                    <table class="table table-hover">
                        <tbody v-if="game">
                          <tr>
                            <td class="el-table_3_column_8">
                                Total Balls
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.total_balls||0}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Free Balls
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.free_balls||0}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Game Type
                            </td>
                            <td class="el-table_3_column_8">
                               {{$_$gameTypes.find(e=>e.value==game.type).name}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Price
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.price}} GBP
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Remaining Paid
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.left_paid}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Remaining Free
                            </td>
                            <td class="el-table_3_column_8">
                               {{game.left_free||0}}
                            </td>
                        </tr>
                         <tr>
                            <td class="el-table_3_column_8">
                                Prize
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.prize}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Prize Description
                            </td>
                            <td class="el-table_3_column_8">
                                {{game.prize_description}}
                            </td>
                        </tr>
                        <tr>
                            <td class="el-table_3_column_8">
                                Prize Photo
                            </td>
                            <td class="el-table_3_column_8">
                               <el-button type="text" @click="prev_photo = true">Preview Prize</el-button>
                            </td>
                        </tr>
                        </tbody>
                        <tbody v-else>
                            <tr>
                                <td class="el-table_3_column_8" align-center colspan="6">
                                No Data found for this game
                            </td>
                            </tr>
                        </tbody> 
                      </table>
                </div>
            </div>


        </div>

        <spinner :show.sync="loading"/>
        <el-dialog :visible.sync="prev_photo" v-if="prev_photo" width="70%">
            <img width="100%" :src="$API+'/contents/'+game.prize_photo" alt="">
        </el-dialog>
        <Min :show="minPaid" />
    </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import vueinterval from 'vue-interval/dist/VueInterval.common'
import Min from './min_paid';
export default {
    mixins:[vueinterval],
    components: {Min},
    data(){
        return {
            details: null,
            preview: false,
            edit: false,
            prev_photo: false,
            current: 1,
            refreshTime: 0,
        }
    },
    mounted(){
        this.fetchLive();
        
    },
    methods: {
        ...mapActions('live_admin', [
            'getLiveGamePlayer',
            'refreshPlayer'
        ]),
        ...mapActions('player', [
            'getIPAddress',
            'updatePicks',
            'logout_user',
            'setUser'
        ]),
        logout(){
            this.logout_user();
        },
        INTERVAL__1e4$myUpdateMethod: function () { 
            console.log("fo");
                       
            this.refreshTime+=10;
            if(this.refreshTime == 20){
                if(this.completed){
                    this.refreshData();
                }
                this.refreshTime = 0;
            }
        },
        
        fetchLive(){
            var args = {
                Id: this.$route.params.gameId
            };
            this.getLiveGamePlayer(args).
            then(result=>{
                
            })
        },
        refreshData(){
            if(this.user.registered){
                var args = {
                    Id: this.$route.params.gameId
                };
                this.refreshPlayer(args).
                then(result=>{
                    this.current = 1;
                })
            }
        },
        isWinner(item){
            if(this.game){
                if(this.game.winnerArray){
                    if(this.game.winnerArray.includes(item)){
                        var position = this.game.winnerArray.indexOf(item);
                        position++;
                        return this.positional(position)+" Winner!!!";
                    }                    
                }
            }
        },
        positional(it){
            if(it == 1){
                return '1st';
            }else if(it == 2){
                return '2nd';
            }else if(it == 3){
                return '3rd';
            }else if(it > 3){
                return it+'th';
            }
        },
        disable(status){
            if(status !== 'Not picked yet'){
                return true
            }
            return false
        },
        picker(ball){
            if(this.game.picks[ball].status == 'Not picked yet'){
                var g = this.game;
                g.picks[ball].status = 'picked';
                if(g.type !== '4'){
                    if(g.auth_type == '1'){
                        if(g.frees.filter(e=>e==g.picks[ball].ball).length > 0){
                            g.picks[ball].status = 'free';
                            g.left_free = g.left_free - 1;
                        }else{
                            g.picks[ball].status = 'paid';
                            g.left_paid = g.left_paid - 1;
                        }
                    }
                }
                if(g.type == '4'||g.type == '2'){
                    g.picks[ball].status = 'paid';
                    g.left_paid = g.left_paid - 1;
                }
                
                g.picks[ball].ip = this.ip;
                g.picks[ball].player = this.user.first_name+' '+this.user.last_name;
                g.picks[ball].time = new Date().getTime();
                var args = {
                    game: g,
                    id: this.$route.params.gameId
                };
                this.updatePicks(args).
                then(edr=>{
                    this.fetchLive();
                })
            }else{
                this.$notify({
                    title: 'This Ball is taken.',
                    type: 'error'
                });
            }
            
        },
        
        closeEdit(){
            this.preview = false;
            this.edit = false;
        },
        openEdit(){
            this.edit = true;
        },
        openShare(){
            this.preview = true;
        },
        getColor(status){
            var color = 'light-green darken-4';
            if(status=='paid'){
                color = 'amber darken-4'
            }
            if(status =='free'){
                color='indigo darken-3';
            }
            return color;
        },
        getDate(time){
            if(time !== 'Not picked yet'){
                var day = new Date(time).getDate();
                var month = new Date(time).getMonth();
                var year = new Date(time).getFullYear();
                var hours = new Date(time).getHours();
                var min = new Date(time).getMinutes();
                var sec = new Date(time).getSeconds();
                var times = day+'/'+month+'/'+year+' - '+hours+':'+min+':'+sec;
                return times;
            }else{
                return time;
            }
        },
        register(){
            this.$validator.validateAll().then(result=>{
                if(result){
                    var args = true;
                    this.getIPAddress(args).
                    then(ed=>{
                        location.href = location.href;    
                    }) 
                }else{
                    this.$notify({
                        title: 'Some Details are Missing',
                        type: 'error'
                    });
                }                   
            });
                   
        },
        

    },
    computed: {
        ...mapGetters('player', [
            'user',
            'ip'
        ]),
        
        ...mapGetters('live_admin', [
            'game',
            'loading',
        ]),

        isRegistered(){
            if(this.user.full_name && this.user.ip){
                return true                
            }
            return false
        },
        tableData(){
            var tb = [];
            tb = [
                {label: 'ID', value: this.details?this.game.game_id:''},
                {label: 'Game Type', value: this.details?this.$_$gameTypes.find(e=> e.value === this.type).name:''},
                {label: 'Total Balls', value: this.details?this.details.total_balls:''},
                {label: 'Price', value: this.details?''+this.details.price:'0'},
            ];
            return tb;
        },
        photo(){
            var t = this.details?this.details.prize_photo:'';
            return this.$API+t;
        },
        Data(){
            var d = this.details?this.details.picks:[];
            return d;
        },
        type(){
            if(this.details){
                var d = this.details.type;
                return d;
            }else{
                return '0';
            }
            
        },
        minPaid(){
           var con1 = this.game.left_paid === 0,
            con2 = this.game.min_paid >= this.game.left_paid,
            con3 = this.game.picks_completed,
            con4 = this.game.left_free === 0;
            if((!con1 &&con2 && !con3)||(!con4 &&con2 && !con3)){
                return true
            }else{
                return false
            }
        },
        completed(){
            var c = true;
            if(this.details){
                if(this.details.game_completed){
                    c = false
                    return c;
                }
            }
            return c;
        },
        timer(){
            

            
        //    if(this.user.registered && this.completed){
        //        setInterval(()=>{
        //         this.current+=1;
        //         if(this.current == 30 ){
        //             this.refreshData();
        //         }
        //     }, 1000);
        //    }
           
        }

    }
}
</script>

<style scoped>
    .winner {
        text-shadow: 0 0 10px red;
        -webkit-transition: text-shadow 0.2s linear;
        -moz-transition: text-shadow 0.2s linear;
        -ms-transition: text-shadow 0.2s linear;
        -o-transition: text-shadow 0.2s linear;
        transition: text-shadow 0.2s linear;
    }
     .add-margin {
        margin-left: -10px;
    }
</style>





