<template>
	<div class="container">
		<section>
			<div class="row px-4">
				<div class="col-xs-12 col-lg-3 col-md-3">
					<div class="people stats-card row ml-1">
						<div class="col-md-7 col-xs-7">
							<span class="stats-title">Group Games</span>
							<p class="stats-val">{{Games.length}}</p>
						</div>
						<div class="stats-icon bg-base-2 fa-2x col-md-4 img-rounded text-center pull-right" >
							<i class="fa fa-users"></i>
						</div>
					</div>
				</div>
				<div class="col-xs-12 col-lg-3 col-md-3 px-3">
					<div class="people stats-card row ml-1">
						<div class="col-md-7 col-xs-7">
							<span class="stats-title">Completed Games</span>
							<p class="stats-val">{{completed}}</p>
						</div>
						<div class="stats-icon bg-base-2 fa-2x col-md-4 img-rounded text-center pull-right" >
							<i class="fa fa-gamepad"></i>
						</div>
					</div>
				</div>
				<div class="col-xs-12 col-lg-3 col-md-3">
					<div class="people stats-card row ml-1">
						<div class="col-md-7 col-xs-7">
							<span class="stats-title">Ongoing Games</span>
							<p class="stats-val">{{ongoing}}</p>
						</div>
						<div class="stats-icon bg-base-2 fa-2x col-md-4 img-rounded text-center pull-right" >
							<i class="fa fa-bomb"></i>
						</div>
					</div>
				</div>
				<div class="col-xs-12 col-lg-3 col-md-3">
					<div class="people stats-card row ml-1">
						<div class="col-md-7 col-xs-7">
							<span class="stats-title">Mini Games</span>
							<p class="stats-val">{{minis}}</p>
						</div>
						<div class="stats-icon bg-base-2 fa-2x col-md-4 img-rounded text-center pull-right" >
							<i class="fa fa-beer"></i>
						</div>
					</div>
				</div>
			</div>
		</section>	

		<section>
			<div class="row px-4 mt-3">
				<div class="col-xs-10">
					<h5 class="heading">Existing Games ({{Games.length}})</h5>
				</div>
				<div class="col-xs-1">
					<!-- <el-button class="pull-right bg-base-2" size="large" circle @click="openGameConfig">
						<i class="fa fa-plus"></i>
					</el-button> -->
				</div>
		    
			</div>
		</section>
		<section v-if="loading">
			<div class="row px-4 mt-3 text-center">
				<span>
					<i large class="blue--text lighten-2 fa fa-spinner fa-spin fa-3x"></i>
				</span>	    
			</div>
		</section>

		<div v-else>
			<section v-if="Games.length === 0">
			<div class="row px-4 mt-3 text-center">
				<span>
					<i large class="blue--text lighten-2 fa fa-folder-open-o fa-3x"></i>
					<p class="heading">No Games Found</p>
				</span>	    
			</div>
		</section>

		<section v-else>
			<div class="row px-1 mt-3">
				<div class="col-xs-12 col-md-6 col-lg-3" v-for="type in Games" :key="type.Id" >
			      	<card :details="type" @refresh="fetchGames" :game="type.Id"/>
			    </div>
			</div>
		</section>
		</div>    
	</div>
</template>
<style type="text/css" scoped>
	.no-games {
		text-align: center;
	}
	.wht {
		line-height: 100px;
	}
	.count {
		line-height: 20px;
		font-size: 18px;
		font-style: italic;
	}


</style>


<script type="text/javascript">
  import { mapGetters, mapActions } from "vuex";
  import card from './gameCard'

  export default {
 	data(){
 		return {
 			ready: false,
 		}
 	},
 	components:{card},
 	mounted(){
 		this.fetchGames();
 	},
 	computed: {
		...mapGetters('user_mgt', [
			'user',
			'session'
		]),
		Games(){
			if(this.all_games){
				var g = this.all_games.filter(e=>e.active!==false); 
				return g.reverse();
			}
			return [];
		},
		...mapGetters('games', [
			'all_games',
			'loading',
			'mini_games',
			'group_games',

		]),
		ongoing(){
			var ongoin = 0;				
			if(this.ready){				
				ongoin = this.Games.filter(e=>!e.game_completed).length;
			}
			return ongoin;
		},
		completed(){
			var completed = 0;
			if(this.ready){
				completed = this.Games.filter(e=>e.game_completed).length;
			}
			return completed;	
		},
		minis(){
			var min = 0;
			if(this.ready){
				min = this.Games.filter(e=>e.type === '4').length;
			}
			return min;
					
		},
 	},
 	methods: {
 		...mapActions('games',['allgames']),
 		fetchGames(){
			 var self = this;
 			var args = {
 				session: this.session,
 			}
 			this.allgames(args)
 			.then(()=>{
				self.ready = true; 
 			})
 		},
		gameDetails(game){
			return JSON.parse(game.details);
		},
 		openGameConfig(){
 			this.$router.push({name: 'game-config'});
 		}
 	}	
  }
</script>




