<template>
	<el-dialog
	    fullscreen
	    :visible.sync="show"
	    :show-close="false">

	   <div class="container">
        <div class="row">
            <div class="col-xs-12 col-md-4 col-lg-4">
                
            </div>
            <div class="col-xs-12 col-md-4 col-lg-4 text-center pt-5 mt-5">
                Please Select Existing Players for the remaining Balls
                <div class="table-responsive">
                    <table class="table table-hover">
                        <thead>
                          <tr>
                            <th>
                                Balls
                            </th>
                            <th >
                                Player
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(item, index) in game.picks" v-if="item.status === 'Not picked yet'" :key="index" style="padding-top: 10px;">
                            <td class="el-table_3_column_8" width="70px">
                                <el-button circle class="purple white--text" dark small> 
                                    {{item.ball}}
                                </el-button>
                            </td>
                            <td>
                               <el-select v-model="currentName" @change="assign(index)" placeholder="Select One Player">
                                   <el-option v-for="(pl, m) in players"
                                       :key="m"
                                       :label="pl.player"
                                       :value="pl.player">
                                   </el-option>
                               </el-select>
                            </td>
                        </tr>
                        </tbody>
                      </table>
                </div>
            </div>
            <div class="col-xs-12 col-md-4 col-lg-4">
                
            </div>
        </div>

    </div>
	    
	</el-dialog>
</template>

<script type="text/javascript">
	export default {
		props: ['show','players', 'game'],
		data(){
			return {
				currentName: '',
			}
		},
		methods: {
			assign(index){
				var th = this.currentName;
				this.currentName = '';
				var pl = this.players.find(e=>e.player == th);
				console.log(index);
				this.game.picks[index].player = th;
				this.game.picks[index].time = new Date().getTime();
				this.game.picks[index].ip = pl.ip;
				this.$emit('save', index);
			}
		}
	}
</script>


